import React, { useState, Fragment } from 'react'
import { omit } from 'lodash-es'
import { getDateString } from '../../helpers/dateParser'

import Box from '@mui/material/Box'
import Grid from '@mui/material/Grid'
import Button from '@mui/material/Button'
import Divider from '@mui/material/Divider'
import Typography from '@mui/material/Typography'

import ArrowLeft from '@mui/icons-material/ArrowLeft'
import ArrowRight from '@mui/icons-material/ArrowRight'

import DialogMember from '../ui/DialogMember'
import DialogSalePoint from '../ui/DialogSalePoint'

const OrderList = ({
  sales,
  fetchSales,
  currentOrderId,
  setCurrentOrderId,
  currentPaidOrder,
}) => {
  const [searchContent, setSearchContent] = useState({})
  const [isMemberOpen, setIsMemberOpen] = useState(false)
  const [isSalePointOpen, setIsSalePointOpen] = useState(false)

  const handleSearch = () => {
    fetchSales({
      isReturn: 0,
      ...omit(searchContent, ['memberName', 'salePointNames']),
    })
  }

  const onSearchChange = e => {
    setSearchContent({
      ...searchContent,
      [e.target.name]: e.target.value,
    })
  }

  const changeDate = e => {
    const changeType = e.currentTarget.dataset.changeType
    const originStartValue = searchContent.startDate
    const originEndValue = searchContent.endDate

    if (!originStartValue && !originEndValue) return
    const newSearchContent = {
      ...searchContent,
      ...(originStartValue && { startDate: getDateString(originStartValue, changeType) }),
      ...(originEndValue && { endDate: getDateString(originEndValue, changeType) }),
    }
    setSearchContent(newSearchContent)
  }

  const onPaidOrderClick = id => {
    setCurrentOrderId(id)
    // setSelectedIds([])
    // setIsSelectedAll(false)
  }

  const handleMemberSelect = member => {
    setSearchContent(prev => ({
      ...prev,
      memberId: member.id,
      memberName: member.memberName,
    }))

    setIsMemberOpen(false)
  }

  const handleMemberClear = () => {
    setSearchContent(prev => omit(prev, ['memberId', 'memberName']))
  }

  const handleSalePointSelect = salePoints => {
    const salePointIdsArrayString = salePoints.map(el => el.id).join(',')
    const salePointNamesArrayString = salePoints.map(el => el.salePointName).join(',')

    setSearchContent(prev => ({
      ...prev,
      salePointIds: salePointIdsArrayString,
      salePointNames: salePointNamesArrayString,
    }))
    setIsSalePointOpen(false)
  }

  const handleSalePointClear = () => {
    setSearchContent(prev => omit(prev, ['salePointIds', 'salePointNames']))
  }

  const renderOrders = orders => {
    return orders.list && orders.list.map((order, index) => {
      const active = currentOrderId === order.id ? ' active' : '';
      return (
        <div className={`order-item ${active}`} key={order.id} onClick={() => onPaidOrderClick(order.id)}>
          <div className="serial-sec">{index + 1}</div>
          <div className="time-sec">{formatDateString(order.date)}</div>
          <div className="total-sec">{order.total}</div>
          <div className="actual-total-sec">{order.actualTotal}</div>
          <div className="member-name-sec">{order.memberInfo ? order.memberInfo.memberName : ''}</div>
          <div className="member-phone-sec">{order.memberInfo ? order.memberInfo.phone : ''}</div>
          {/* <div className="invoice-sec">SR19356271</div> */}
        </div>
      )
    })
  }

  const renderOrderDetail = details => {
    return details.map((detail, index) => {
      return (
        <div className="table-item" key={detail.id}>
          {/* <div className="checkbox-sec sec-item">
            <Checkbox
              className="checkbox-color"
              color="default"
              size="small"
              checked={selectedIds.includes(detail.id)}
              onChange={(e) => onCommCheckboxClick(e, detail.id)}
            />
          </div> */}
          <div className="serial-sec sec-item">{index + 1}</div>
          <div className="name-sec sec-item">{detail.stockDescription || detail.stockMaster.description}</div>
          <div className="price-sec sec-item">{detail.price}</div>
          <div className="quantity-sec sec-item">{detail.quantity}</div>
          <div className="discount-sec sec-item">
            {((detail.price * detail.quantity) - detail.subTotal)}
          </div>
          <div className="total-sec sec-item">{detail.subTotal}</div>
        </div>
      )
    })
  }

  const formatDateString = timestamp => {
    const inputDate = new Date(timestamp);
    const yearString = inputDate.getFullYear().toString();
    const month = inputDate.getMonth() + 1;
    const monthString = month > 9 ? month.toString() : '0' + month.toString();
    const date = inputDate.getDate();
    const dateString = date > 9 ? date.toString() : '0' + date.toString();
    const dayString = dayStrings[inputDate.getDay()];
    const originTimeString = inputDate.toLocaleTimeString().replace('午', '午 0').slice(0, -3);
    const timeString = originTimeString.length > 8 ? originTimeString.replace(' 01', ' 1') : originTimeString;
    return yearString + '/' + monthString + '/' + dateString + ' (' + dayString + ') ' + timeString;
  }


  return (
    <Fragment>
      {/* <Typography variant="h6"
        sx={{
          color: (theme) => theme.palette.primary.main,
          fontSize: '1.25rem',
          fontWeight: 'bold',
          letterSpacing: '0.05em',
        }}
      >
        訂單清單
      </Typography> */}

      <Box display="flex" sx={{ pb: '1.25rem', }}>
        <Grid
          container
          className="search-field-container"
          sx={{
            flexGrow: 1,
            // flexWrap: 'nowrap',
            alignItems: 'center',
            justifyContent: 'space-between',
            fontWeight: 'bold',
            '& label': {
              flexShrink: 0,
              fontSize: '1rem',
            },
            '& input': {
              border: '1px solid #c4c4c4',
              borderRadius: '0.25em',
              display: 'inline-block',
              minWidth: 0,
            },
            '& select': {
              lineHeight: 1.75,
              border: '1px solid #c4c4c4',
              borderRadius: '0.25em',
              display: 'inline-block',
              minWidth: 0,
            },
            '& input.error::placeholder': {
              color: '#cb5c5c',
            },
          }}
        >

          <Box sx={fieldGridStyle}>

            <Box sx={btnFieldStyle}>
              <label>會員 :</label>
              <input type="text" value={searchContent.memberName || ''} readOnly />
              <Button
                disableElevation
                variant="contained"
                onClick={() => setIsMemberOpen(true)}
              >
                查詢
              </Button>
              <Button
                variant="contained"
                disableElevation
                onClick={handleMemberClear}
              >
                清除
              </Button>

              <DialogMember
                open={isMemberOpen}
                onSelectClick={handleMemberSelect}
                handleClose={() => setIsMemberOpen(false)}
              />
            </Box>

          </Box>

          <Box sx={fieldGridStyle}>

            <Box sx={btnFieldStyle}>
              <label>門市 :</label>
              <input type="text" value={searchContent.salePointNames || ''} readOnly />
              <Button
                disableElevation
                variant="contained"
                onClick={() => setIsSalePointOpen(true)}
              >
                查詢
              </Button>
              <Button
                variant="contained"
                disableElevation
                onClick={handleSalePointClear}
              >
                清除
              </Button>

              <DialogSalePoint
                open={isSalePointOpen}
                onSalePointSelectClick={handleSalePointSelect}
                handleClose={() => setIsSalePointOpen(false)}
              />
            </Box>

          </Box>

          <Box
            sx={{
              flexGrow: 1,
              maxWidth: '100%',
              display: 'flex',
              justifyContent: 'space-between',
              // '@media (min-width: 960px)': {
              //   flex: '1 0 31%',
              //   maxWidth: '31%',
              // },
            }}
          >
            <Box
              sx={{
                display: 'flex',
                alignItems: 'center',
                position: 'relative',
                flexGrow: 1,
                '& .date-input-wrapper': {
                  pl: '1rem',
                  display: 'flex',
                  flexGrow: 1,
                  minWidth: 0,
                  '& input': {
                    width: '100%',
                  },
                },
                '& .date-control': {
                  flex: '0 0 auto',
                  color: '#767676',
                  cursor: 'pointer',
                },
              }}
            >
              <label className="label">銷售日期 :</label>

              <div className="date-input-wrapper">
                <input
                  type='date'
                  name="startDate"
                  className="date-input"
                  onChange={onSearchChange}
                  max={searchContent.endDate}
                  value={searchContent.startDate || ''}
                />
              </div>

              <div className="date-input-wrapper">
                <input
                  type='date'
                  name="endDate"
                  className="date-input"
                  onChange={onSearchChange}
                  min={searchContent.startDate}
                  value={searchContent.endDate || ''}
                />
              </div>

              <div className="date-control">
                <ArrowLeft viewBox="1 2.5 20 20" data-change-type="previous" onClick={changeDate} />
                <ArrowRight viewBox="4 2.5 20 20" data-change-type="next" onClick={changeDate} />
              </div>
            </Box>
          </Box>


        </Grid>

        <Box
          sx={{
            pl: '1rem',
            display: 'flex',
          }}
        >
          <Button
            onClick={handleSearch}
            sx={{
              color: 'background.paper',
              fontSize: '0.875rem',
              bgcolor: (theme) => theme.palette.primary.main,
              '&:hover': {
                bgcolor: 'primary.light',
              },
            }}
          >
            搜尋
          </Button>
        </Box>


      </Box>

      <div className="search-result">
        <div>
          <Box sx={orderHeaderStyle}>
            <div className="serial-sec">項次</div>
            <div className="time-sec">交易時間</div>
            <div className="total-sec">總和</div>
            <div className="actual-total-sec">總金額</div>
            <div className="member-name-sec">會員姓名</div>
            <div className="member-phone-sec">會員電話</div>
          </Box>
          <Box sx={orderListStyle}>
            {renderOrders(sales)}
          </Box>
        </div>

        <Divider sx={{ mt: '1.5rem', mb: '0.75rem', }} />

        <Typography variant="h6"
          sx={{
            color: (theme) => theme.palette.primary.main,
            fontSize: '1.25rem',
            fontWeight: 'bold',
            letterSpacing: '0.05em',
          }}
        >
          交易明細
        </Typography>
        <Box sx={orderDetailStyle}>
          <div className="table-header">
            <div className="serial-sec sec-item">項次</div>
            <div className="name-sec sec-item">商品名稱</div>
            <div className="price-sec sec-item">定價</div>
            <div className="quantity-sec sec-item">數量</div>
            <div className="discount-sec sec-item">折數</div>
            <div className="total-sec sec-item">合計</div>
          </div>

          <Box className="table-list">
            {(currentPaidOrder && currentPaidOrder.salesDetails)
              ? renderOrderDetail(currentPaidOrder.salesDetails)
              : null}
          </Box>
        </Box>
      </div>

    </Fragment>
  )
}

const dayStrings = ['日', '一', '二', '三', '四', '五', '六']

const fieldGridStyle = {
  flex: '0 0 100%',
  maxWidth: '100%',
  display: 'flex',
  justifyContent: 'space-between',
  '@media (min-width: 600px)': {
    flex: '0 0 49%',
    maxWidth: '49%',
  },
}

const orderHeaderStyle = {
  display: 'flex',
  color: 'background.darkPaper',
  bgcolor: 'secondary.main',
  '& > div': {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    fontSize: '1rem',
    borderRight: theme => `1px solid ${theme.palette.background.darkPaper}`,
    paddingTop: '0.125em',
    paddingBottom: '0.125em',
  },
  '& .serial-sec': {
    flex: '0 0 7%',
    borderLeft: theme => `1px solid ${theme.palette.background.darkPaper}`,
  },
  '& .time-sec': {
    flexGrow: 1,
  },
  '& .total-sec': {
    flex: '0 0 12.5%',
  },
  '& .actual-total-sec': {
    flex: '0 0 12.5%',
  },
  '& .member-name-sec': {
    flex: '0 0 15%',
  },
  '& .member-phone-sec': {
    flex: '0 0 17.5%',
  },
  '& .invoice-sec': {
    flex: '0 0 17.5%',
    justifyContent: 'flex-start',
    borderRight: 'none',
    padding: '0 1em',
  },
}

const orderListStyle = {
  maxHeight: '8rem',
  overflow: 'overlay',
  '& .order-item': {
    display: 'flex',
    cursor: 'pointer',
    userSelect: 'none',
    bgcolor: 'background.darkPaper',
    '& > div': {
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      fontSize: '1rem',
      color: 'secondary.main',
      height: '2em',
      padding: '0.25em',
      borderRight: '1px solid #f0f0f0',
      overflow: 'overlay',
    },
    '&:nth-of-type(2n + 1)': {
      bgcolor: '#e6e6e6',
    },
    '& .serial-sec': {
      flex: '0 0 7%',
      borderLeft: theme => `1px solid ${theme.palette.background.darkPaper}`,
    },
    '& .time-sec': {
      flexGrow: 1,
    },
    '& .total-sec': {
      flex: '0 0 12.5%',
    },
    '& .actual-total-sec': {
      flex: '0 0 12.5%',
    },
    '& .member-name-sec': {
      flex: '0 0 15%',
    },
    '& .member-phone-sec': {
      flex: '0 0 17.5%',
    },
    '& .invoice-sec': {
      flex: '0 0 17.5%',
      justifyContent: 'flex-start',
      borderRight: 'none',
      padding: '0 1em',
    },
    '&.active': {
      bgcolor: 'active.main',
    },
  },
}

const orderDetailStyle = {
  display: 'inline-block',
  width: '100%',
  '& .table-header': {
    display: 'flex',
    height: '2em',
    color: 'background.paper',
    fontSize: '1rem',
    bgcolor: 'secondary.main',
  },
  '& .table-list': {
    display: 'flex',
    flexDirection: 'column',
    overflow: 'overlay',
    height: '10rem',
  },
  '& .table-item': {
    flex: '0 0 25%',
    display: 'flex',
    color: 'secondary.main',
    fontSize: '1rem',
    bgcolor: 'background.light',
    cursor: 'pointer',
    userSelect: 'none',
  },
  '& .table-item:nth-of-type(2n + 1)': {
    bgcolor: '#ebebeb',
  },
  '& .table-item.active': {
    bgcolor: 'active.main',
  },
  '& .sec-item': {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    textAlign: 'center',
    padding: '0.25em',
    borderRight: '1px solid #eeeeee',
    minWidth: 0,
  },
  '& .checkbox-sec': {
    flex: '0 0 6%',
  },
  '& .serial-sec': {
    flex: '0 0 7%',
  },
  '& .name-sec': {
    flex: '1 0 20%',
    wordBreak: 'break-all',
  },
  '& .price-sec': {
    flex: '0 0 12.5%',
  },
  '& .quantity-sec': {
    flex: '0 0 10%',
  },
  '& .discount-sec': {
    flex: '0 0 10%',
  },
  '& .total-sec': {
    flex: '0 0 12.5%',
  },
  '& .checkbox-color': {
    color: (theme) => theme.palette.primary.main,
    padding: '0.25em',
  },
}

const btnFieldStyle = {
  pb: '0.5rem',
  minWidth: 0,
  display: 'flex',
  flexGrow: 1,
  alignItems: 'center',
  position: 'relative',
  '& label': {
    pr: '1rem',
  },
  '& input': {
    padding: '0 0.25em',
    flexGrow: 1,
    '&:disabled': {
      color: '#000000',
      bgcolor: '#fbfbfb',
    },
  },
  '& button': {
    padding: '0 7px',
    minWidth: '41px',
    marginLeft: '0.25rem',
    marginTop: '0.125rem',
    marginBottom: '0.125rem',
    color: 'background.paper',
    bgcolor: (theme) => theme.palette.primary.main,
    '&:hover': {
      bgcolor: 'primary.light',
    },
  },
}

export default OrderList