import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import CancelIcon from '@mui/icons-material/Cancel'
import SendIcon from '@mui/icons-material/Send'
import { Box, Button, Paper, Grid, Divider, Dialog, DialogContent, IconButton, Typography } from '@mui/material'
import SalePointCard from './SalePointCard'
import { salePointRequest } from '../../apis'

function DialogSalePoint({
  auth,
  open,
  handleClose,
  onSalePointSelectClick,
}) {
  const [salePoints, setSalePoints] = useState([])
  const [selectedSalePoints, setSelectedSalePoints] = useState([])

  useEffect(() => {
    const fetchSalePoints = async () => {
      const { data } = await salePointRequest.get('', {
        headers: { 'Authorization': 'Bearer ' + auth.authenticated },
      })
      setSalePoints(data)
    }
    fetchSalePoints()

    return () => {
      setSalePoints([])
    }
  }, [auth.authenticated])


  const handleSelectSalePoint = salePoint => {
    const newSelectedSalePoints = selectedSalePoints.some(el => el.id === salePoint.id)
      ? selectedSalePoints.filter(el => el.id !== salePoint.id)
      : [...selectedSalePoints, salePoint]
    setSelectedSalePoints(newSelectedSalePoints)
  }

  const handleSubmit = () => {
    onSalePointSelectClick(selectedSalePoints)
  }

  const onClose = () => {
    setSelectedSalePoints([])
    handleClose()
  }

  const renderSalePointCards = (salePoints, colNum) => {
    return salePoints.map(salePoint => (
      <div className="card-grid" key={salePoint.id}>
        <SalePointCard
          salePoint={salePoint}
          selectedSalePoints={selectedSalePoints}
          handleSelectSalePoint={handleSelectSalePoint}
        />
      </div>
    ))
  }

  const renderSelectedSalePoints = salePoints => {
    return salePoints.map(salePoint => (
      <Grid item xs={12} key={salePoint.id}>
        <Paper elevation={0} square>
          <Typography variant="subtitle2">
            {salePoint.salePointName}
          </Typography>
        </Paper>
      </Grid>
    ))
  }

  return (
    <Dialog
      id="dialog-member"
      open={open}
      onClose={onClose}
      fullWidth
      maxWidth="sm"
      aria-labelledby="dialog-member"
      PaperProps={{
        sx: { maxWidth: 800 }
      }}
    >
      <Box
        sx={{
          margin: 0,
          padding: '0.375em 1.75em 0.25em',
          bgcolor: 'background.default',
          '& h6': {
            color: 'secondary.main',
            fontSize: '1.25rem',
            fontWeight: 'bold',
          }
        }}
      >
        <Typography variant="h6">門市選取</Typography>

        {handleClose ? (
          <IconButton aria-label="close" onClick={handleClose} sx={{
            position: 'absolute',
            right: '0.5em',
            top: '0.25em',
            color: '#cb5c5c',
            padding: 0,
          }}>
            <CancelIcon sx={{ width: '1.25em', height: '1.25em' }} />
          </IconButton>
        ) : null}
      </Box>

      <DialogContent sx={{ padding: '0.25em 1.75em 2em', bgcolor: 'background.paper' }}>

        <Grid
          container
          alignItems="flex-start"
          // wrap="nowrap"
          sx={{
            mt: '1.5rem',
            p: '0.5rem 0.25rem',
            bgcolor: '#eeeeee',
            borderRadius: 2,
            '& hr': {
              height: 'unset',
            },
          }}>
          <Grid
            xs={12}
            md
            item
          >
            <Typography variant="subtitle1" sx={{
              p: '0.25rem 0.5rem',
              fontWeight: 'bold',
            }}>
              門市列表
            </Typography>
            <Grid
              container
              sx={{
                px: '0.5rem',
                pb: '2rem',
                maxHeight: 400,
                overflowY: 'auto',
                display: 'flex',
                gap: '1rem',
                '& .MuiCard-root': {
                  p: '1.5rem 1rem',
                },
                '& .salePoint-name': {
                  flex: '0 0 100%',
                },
                '& .card-grid': {
                  flex: '0 0 calc(50% - 0.5rem)',
                  maxWidth: 'calc(50% - 0.5rem)',
                  '& .MuiPaper-root': {
                    bgcolor: 'background.default',
                  },
                },
              }}
            >
              {renderSalePointCards(salePoints, 6)}
            </Grid>
          </Grid>

          <Divider orientation="vertical" variant="middle" flexItem sx={{ mx: '1rem' }} />

          <Grid
            xs={12}
            md={3}
            item
          >
            <Typography variant="subtitle1" sx={{
              p: '0.25rem 0.5rem',
              fontWeight: 'bold',
            }}>
              已選取
            </Typography>
            <Grid
              container
              // spacing={1}
              direction="column"
              wrap="nowrap"
              sx={{
                pl: '0.25rem',
                pr: '0.5rem',
                maxHeight: 400,
                overflowY: 'auto',
                '& .MuiPaper-root': {
                  p: '1.5rem 2rem',
                  mb: '1rem',
                },
                '& .salePoint-id': {
                  pt: '0.25rem',
                  color: 'text.secondary',
                },
              }}
            >
              {renderSelectedSalePoints(selectedSalePoints)}
            </Grid>
          </Grid>

        </Grid>

        <Box component="form" sx={formFieldStyle}>

          <div className="submit-field">
            <Button
              size="large"
              className="submitBtn primaryBtn"
              onClick={handleSubmit}
              endIcon={<SendIcon />}
            >
              確認
            </Button>
          </div>
        </Box>


      </DialogContent>
    </Dialog>
  )
}

const formFieldStyle = {
  display: 'flex',
  p: '1.5em 0 0rem',
  alignItems: 'center',
  justifyContent: 'flex-end',
  '& .input-field': {
    flex: '0 0 30%',
    color: 'secondary.main',
    paddingRight: '0.5em',
  },
  '& .input-wrapper': {
    padding: '0.375em 0',
  },
  '& label': {
    fontWeight: 'bold',
    fontSize: '1.125rem',
  },
  '& input': {
    width: 'calc(100% - 4em)',
    lineHeight: 1.25,
    padding: '0 0.25em',
    '&::placeholder': {
      fontSize: '0.875rem',
      letterSpacing: '0.04em',
    },
    '&.error::placeholder': {
      color: 'error.light',
    },
  },
  '& .submit-field': {
    flex: '0 0 18%',
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'center',
    '& .submitBtn': {
      flex: '0 0 100%',
      fontWeight: 'bold',
      fontSize: '1.125rem',
      display: 'inline-flex',
      justifyContent: 'center',
      // borderRadius: '0.5em',
      // border: 'none',
    },
    '& .primaryBtn': {
      color: 'background.paper',
      bgcolor: (theme) => theme.palette.primary.main,
      '&:hover': {
        bgcolor: (theme) => theme.palette.primary.light,
      },
    },
  },
}

const mapStateToProps = (state) => ({
  auth: state.auth,
})

export default connect(mapStateToProps)(DialogSalePoint)