import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import { Toast } from '../../helpers/sweetAlert'
import { useTheme, alpha } from '@mui/system'
import { Checkbox, SvgIcon, Box, Typography } from '@mui/material'
import CancelIcon from '@mui/icons-material/Cancel'
import { replacePosStripsSum } from '../../slices/posStripsSumSlice'
import { replacePosStrips, deletePosStrip } from '../../slices/posStripsSlice'
import { verifyDiscountString, convertDiscount, calcCommTotal } from '../../helpers/discountCalc'

const CommStripList = (props) => {
  const {
    posStrips,
    setUniformContent,
    setKeyboardMode,
    activeCommId,
    setActiveCommId,
    selectedIds,
    setSelectedIds,
    posStripsSum,
    replacePosStripsSum,
  } = props

  const theme = useTheme()
  const [isSelectedAll, setIsSelectedAll] = useState(false)

  // -每次條目更動時，更新總數量及總金額-
  useEffect(() => {
    let primitiveTotal = 0
    let quantity = 0
    let total = 0
    posStrips.forEach((strip) => {
      primitiveTotal += strip.originalTotal
      quantity += strip.quantity
      total += strip.total
    })
    replacePosStripsSum({
      primitiveTotal,
      quantity,
      total,
    })
  }, [posStrips, replacePosStripsSum]);

  const onSelectAllClick = () => {
    if (isSelectedAll) {
      setSelectedIds([])
      setIsSelectedAll(false)
    } else {
      const allStripsId = posStrips.map(strip => strip.id)
      setSelectedIds(allStripsId)
      setIsSelectedAll(true)
    }
  }

  const onCommClick = (id) => {
    setActiveCommId(id)
    setUniformContent('')
  }

  const onInputClick = (e) => {
    setUniformContent('')
    setKeyboardMode(e.target.dataset.keyboardMode)
  }

  const onCommCheckboxClick = (e, itemId) => {
    e.target.checked
      ? setSelectedIds([...selectedIds, itemId])
      : setSelectedIds(
        selectedIds.filter((selectedId) => selectedId !== itemId)
      )
    setUniformContent('')
  }

  const onClearClick = () => {
    setSelectedIds([])
    setActiveCommId('')
    setUniformContent('')
  }

  const handleInputChange = (e, itemId, isNumber) => {
    const processedComms = posStrips.map((strip) => {
      return strip.id === itemId
        ? calcCommTotal({
          ...strip,
          [e.target.name]: isNumber ? Number(e.target.value) : e.target.value,
        })
        : strip
    })
    props.replacePosStrips(processedComms)
  }

  const onQuantityChange = (e, itemId) => {
    if (Number(e.target.value) < 1) return
    handleInputChange(e, itemId, true)
  }

  const onDiscountChange = (e, item) => {
    if (item.undiscountable)
      return Toast.fire({
        title: '警告: 此為不可折扣的商品',
        target: document.querySelector('.right-platform'),
      })
    const discountMode = verifyDiscountString(e.target.value)
    if (!discountMode && e.target.value !== '') return
    const discountAmount = convertDiscount(item, discountMode, e.target.value)
    if (item.price * item.quantity >= discountAmount) {
      handleInputChange(e, item.id)
    } else {
      Toast.fire({
        title: '警告: 折扣額度大於商品價格',
        target: document.querySelector('.right-platform'),
      })
    }
  }

  const renderedStrips = (strips) => {
    return strips.map((strip, index) => {
      const active = strip.id === activeCommId ? 'active' : '';
      return (
        <div
          className={`strip-item ${active}`}
          onClick={() => onCommClick(strip.id)}
          key={strip.id}
        >
          <div className="checkbox-sec">
            <Checkbox
              color="default"
              size="small"
              checked={selectedIds.includes(strip.id)}
              onChange={(e) => onCommCheckboxClick(e, strip.id)}
            />
          </div>
          <div className="serial-sec">{index + 1}</div>
          <div className="name-sec">
            <div>
              <Typography>
                {strip.stockName}
                {strip.undiscountable ? <UndiscountIcon /> : null}
              </Typography>
            </div>
            <div>
              <Typography className="sub" variant="body2">
                商品編號 : {strip.stockId}
              </Typography>
            </div>
          </div>
          <div className="price-sec">{strip.price}</div>
          <div className="quantity-sec">
            <input
              className="strip-input"
              type="number"
              name="quantity"
              data-keyboard-mode="quantityMode"
              value={strip.quantity}
              onClick={onInputClick}
              onChange={(e) => onQuantityChange(e, strip.id)}
            />
          </div>
          <div className="discount-sec">
            <input
              className="strip-input"
              type="text"
              name="discount"
              data-keyboard-mode="discountMode"
              value={strip.discount}
              onClick={onInputClick}
              onChange={(e) => onDiscountChange(e, strip)}
              autoComplete="off"
            />
          </div>
          <div className="total-sec">{strip.total}</div>
          <div className="delete-sec">
            <CancelIcon onClick={() => props.deletePosStrip(strip.id)} />
          </div>
        </div>
      )
    })
  }

  return (
    <Box component="section" id="comm-strip-list" sx={{
      flex: '0 0 43.875%',
      display: 'flex',
      flexDirection: 'column',
      backgroundColor: 'background.darkPaper',
      minHeight: '1px',
      '& .MuiCheckbox-root': {
        color: theme.palette.primary.main,
      },
      '& .Mui-checked': {
        color: theme.palette.primary.main,
      },
    }}>
      {/* Header */}
      <Box className="strip-header" sx={styles.stripHeader}>
        <div className="checkbox-sec">
          <Checkbox
            name="select-all"
            color="default"
            size="small"
            checked={isSelectedAll}
            onChange={onSelectAllClick}
            inputProps={{
              'aria-label': 'select-all-strips',
            }}
          />
        </div>
        <div className="serial-sec">項次</div>
        <div className="name-sec">商品名稱</div>
        <div className="price-sec">定價</div>
        <div className="quantity-sec">數量</div>
        <div className="discount-sec">折數</div>
        <div className="total-sec">合計</div>
        <div className="delete-sec"></div>
      </Box>

      {/* List */}
      <Box className="strip-list" ref={props.listRef} sx={styles.stripList}>
        {renderedStrips(posStrips)}
      </Box>

      {/* Footer */}
      <Box className="strip-footer" sx={{
        flex: '0 0 2.25em',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        fontWeight: 'bold',
        fontSize: '1.125rem',
        color: 'background.paper',
        bgcolor: 'primary.main',
        padding: '0 1em',
        '& .clear-selected-btn': {
          fontSize: '0.95rem',
          lineHeight: 1.5,
          textAlign: 'center',
          verticalAlign: 'middle',
          bgcolor: alpha(theme.palette.text.secondary, 0.05),
          padding: '0.125em 0.75em',
          userSelect: 'none',
          color: 'background.darkPaper',
          border: theme => `1px solid ${theme.palette.background.darkPaper}`,
          transition: 'all 0.2s ease-out',
          '&:hover': {
            bgcolor: 'primary.main',
          },
        },
        '& .total-wrapper': {
          display: 'flex',
          '& svg': {
            width: '0.75em',
            height: '0.75em',
          },
        },
        '& .total-sum': {
          display: 'inline-flex',
          alignItems: 'center',
          padding: '0 0.75em',
        },
        '& .quantity-sum': {
          display: 'inline-flex',
          alignItems: 'center',
          padding: '0 0.75em',
          '& span': {
            paddingLeft: '0.25em',
          },
        },
      }}>
        <input
          className="clear-selected-btn"
          type="button"
          value="清除選取"
          onClick={onClearClick}
        />

        <div className="total-wrapper">
          <div className="quantity-sum">
            <LabelsIcon />
            <span>
              總數量&nbsp;:&nbsp;
              {posStripsSum.quantity}
            </span>
          </div>

          <div className="total-sum">
            <MoneyIcon />
            <span>
              總金額&nbsp;:&nbsp;
              {posStripsSum.total}
              元
            </span>
          </div>
        </div>
      </Box>
    </Box>
  )
}

