const mapStateToProps = (state) => {
  return {
    posStrips: state.posStrips,
    posStripsSum: state.posStripsSum,
  }
}

export default connect(mapStateToProps, {
  replacePosStrips,
  deletePosStrip,
  replacePosStripsSum,
})(CommStripList)
