import React, { useEffect } from 'react'
import { Cancel } from '@mui/icons-material'
import { Box, Dialog, IconButton, Typography, Slide } from '@mui/material'
import DialogCheckoutContent from './DialogCheckoutContent'
import { fetchPaymentMethods } from '../../../slices/paymentMethodsSlice'
import { useDispatch } from 'react-redux'

const DialogCheckout = ({ open, handleCheckoutClose, handleValidateAndClose, isPartialClear, setIsPartialClear }) => {
  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(fetchPaymentMethods())
  }, [dispatch])

  return (
    <Dialog
      id="dialog-checkout"
      open={open}
      TransitionComponent={Transition}
      onClose={handleValidateAndClose}
      aria-labelledby="dialog-checkout"
      fullWidth
      PaperProps={{ sx: { maxWidth: 960 } }}
      sx={{
        '& .MuiBackdrop-root': {
          backgroundColor: 'rgba(0, 0, 0, 0.7)',
        }
      }}
    >
      <Box className="dialog-title" sx={styles.dialogTitle}>
        <Typography variant="h6">結帳畫面</Typography>
        {handleValidateAndClose ? (
          <IconButton aria-label="close" onClick={handleValidateAndClose} sx={styles.titleCloseBtn}>
            <Cancel sx={{ width: '1.25em', height: '1.25em' }} />
          </IconButton>
        ) : null}
      </Box>
      <DialogCheckoutContent
        handleCheckoutClose={handleCheckoutClose}
        isPartialClear={isPartialClear}
        setIsPartialClear={setIsPartialClear}
      />
    </Dialog>
  )
}

const Transition = React.forwardRef((props, ref) => {
  return <Slide direction="up" ref={ref} {...props} />;
})

const styles = {
  dialogTitle: {
    flex: '0 0 auto',
    margin: 0,
    padding: '0.375em 1.75em 0.25em',
    lineHeight: 1.6,
    letterSpacing: '0.0075rem',
    bgcolor: 'background.default',
    '& h6': {
      color: 'secondary.main',
      fontSize: '1.25rem',
      fontWeight: 'bold',
    }
  },
  titleCloseBtn: {
    position: 'absolute',
    right: '0.5em',
    top: '0.25em',
    color: '#cb5c5c',
    padding: 0,
  },
}

export default DialogCheckout