import React, { useState, useEffect } from 'react'
import { v4 as uuidv4 } from 'uuid'
import { connect } from 'react-redux'
import { Box } from '@mui/material'
import { replacePosStrips } from '../../slices/posStripsSlice'
import { replaceCurrentMember } from '../../slices/currentMemberSlice'
import { addTempOrderWithLocal } from '../../slices/tempOrdersSlice'
import { replaceCurrentTempOrderId, replaceCurrentExternalOrderId, replaceCurrentSalesmanId } from '../../slices/currentIdsSlice'

import { Toast } from '../../helpers/sweetAlert'
import { userRequest } from '../../apis'
import Toggle from '../Toggle'
import DialogMember from '../ui/DialogMember'
import DialogTempOrder from './Dialog/DialogTempOrder'
import DialogInfoImport from './Dialog/DialogInfoImport'

const ControlBar = ({
  auth,
  setSelectedIds,
  posStrips,
  posStripsSum,
  currentMember,
  currentTempOrderId,
  currentExternalOrderId,
  currentSalesmanId,
  addTempOrderWithLocal,
  replacePosStrips,
  replaceCurrentTempOrderId,
  replaceCurrentMember,
  replaceCurrentExternalOrderId,
  replaceCurrentSalesmanId,
}) => {

  const [memberOpen, setMemberOpen] = useState(false)
  const [tempLoadOpen, setTempLoadOpen] = useState(false)
  const [importOpen, setImportOpen] = useState(false)
  const [users, setUsers] = useState([])

  useEffect(() => {
    const fetchUsers = async () => {
      const { data } = await userRequest.get('', {
        headers: { 'Authorization': 'Bearer ' + auth.authenticated },
        params: { limit: 'zero' },
      })
      setUsers(data.list)
    }

    fetchUsers()
  }, [auth.authenticated])

  const validateExternalOrder = () => {
    if (currentExternalOrderId) {
      Toast.fire({
        title: '請先取消匯入',
        target: document.querySelector('.right-platform')
      })
      return false
    }
    return true
  }

  const onTempCreateClick = () => {
    if (!validateExternalOrder()) return
    if (posStrips.length < 1) return Toast.fire({
      title: '請至少有一項商品',
      target: document.querySelector('.right-platform')
    })
    const newTempOrder = {
      id: uuidv4(),
      timestamp: Date.now(),
      totalQuantity: posStripsSum.quantity,
      totalAmount: posStripsSum.total,
      member: currentMember,
      content: posStrips,
    }
    addTempOrderWithLocal(newTempOrder)
    setSelectedIds([])
    replacePosStrips([])
    replaceCurrentMember({})
    replaceCurrentTempOrderId('')
    Toast.fire({
      icon: 'success',
      title: '已加入暫存',
      target: document.querySelector('.right-platform')
    })
  }

  const onMemberClick = () => {
    if (!validateExternalOrder()) return
    setMemberOpen(true)
  }

  const onTempLoadClick = () => {
    if (!validateExternalOrder()) return
    setTempLoadOpen(true)
  }

  const onImportClick = () => {
    if (currentTempOrderId) return Toast.fire({
      title: '請先取消暫存',
      target: document.querySelector('.right-platform')
    })
    setImportOpen(true)
  }

  const onCancelMemberClick = () => {
    if (!validateExternalOrder()) return
    replaceCurrentMember({})
  }

  const onCancelTempLoadClick = () => {
    replacePosStrips([])
    replaceCurrentMember({})
    replaceCurrentTempOrderId('')
  }

  const onCancelImportClick = () => {
    replacePosStrips([])
    replaceCurrentMember({})
    replaceCurrentExternalOrderId('')
  }

  const onSelectMemberClick = member => {
    replaceCurrentMember(member)
    setMemberOpen(false)
  }

  const onSalesmanChange = e => {
    replaceCurrentSalesmanId(e.target.value)
  }

  const salesmanOptions = users.map(user => {
    return (
      <option key={user.userId} value={user.userId}>{user.realName}</option>
    )
  })


  return (
    <Box component="section" id="control-bar"
      sx={{
        flex: '0 0 6.125 %',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        bgcolor: 'background.paper',
        padding: '0.5em 0 0.25em',
        '& .salesperson-field': {
          display: 'flex',
          flexWrap: 'wrap',
        },
        '& select': {
          fontSize: '1rem',
          width: '5.5em',
          height: '1.5em',
          padding: '0 0.25em',
          outline: 'none',
          appearance: 'none',
          backgroundPosition: 'right 0.25em top 50%',
          backgroundSize: 'contain',
          backgroundRepeat: 'no-repeat',
          marginTop: '0.25em',
          marginBottom: '0.25em',
        },
        '& select.salesperson-selector': {
          color: 'primary.main',
          bgcolor: 'background.paper',
          borderRadius: '0.125rem',
          textAlign: 'center',
          textOverflow: 'ellipsis',
          border: (theme) => `1px solid ${theme.palette.primary.main}`,
        },
        '& select.split-account-selector': {
          color: '#626262',
          border: '1px solid #aaaaaa',
          borderRadius: '0.125rem',
          backgroundImage: 'url("data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%2224%22%20height%3D%2224%22%20viewBox%3D%220%200%2024%2024%22%3E%3Cpath%20fill%3D%22%23aaaaaa%22%20d%3D%22M7%2010l5%205%205-5z%22%2F%3E%3C%2Fsvg%3E")',
        },
        '& input': {
          fontSize: '1rem',
          lineHeight: 1.5,
          textAlign: 'center',
          verticalAlign: 'middle',
          backgroundColor: 'transparent',
          border: '1px solid transparent',
          userSelect: 'none',
          padding: '0.05em 0.5em',
          margin: '0.25em 0',
          transition: 'all 0.2s ease-out',
          '&:hover': {
            bgcolor: '#f2f2f2',
          },
        },
        '& input.smooth-btn': {
          color: '#aaaaaa',
          border: '1px solid #aaaaaa',
          marginRight: '0.75em',
          borderRadius: '0.25rem',
          '&.active': {
            color: 'background.paper',
            backgroundColor: '#6aa1da',
            border: theme => `1px solid ${theme.palette.background.paper}`,
          },
          '&.block': {
            color: 'background.paper',
            backgroundColor: '#ebebeb',
            border: theme => `1px solid ${theme.palette.background.paper}`,
          },
          '&:last-of-type': {
            mr: 0,
          },
        },
      }}
    >
      <div className="salesperson-field">
        <select className="salesperson-selector" value={currentSalesmanId} onChange={onSalesmanChange}>
          <option value="" disabled>銷售員</option>
          {salesmanOptions}
        </select>
        {/* <select className="split-account-selector">
          <option value="">分帳</option>
        </select> */}
      </div>

      <div>
        {currentExternalOrderId ? (
          <input
            className="smooth-btn active"
            type="button"
            value="取消匯入"
            onClick={onCancelImportClick}
          />
        ) : (
          <input
            className={`smooth-btn ${currentTempOrderId ? 'block' : ''}`}
            type="button"
            value="資料匯入"
            onClick={onImportClick}
          />
        )
        }

        <Toggle determineParam={importOpen}>
          <DialogInfoImport
            open={importOpen}
            handleClose={() => setImportOpen(false)}
          />
        </Toggle>

        {(Object.keys(currentMember).length > 1) ? (
          <input
            className={`smooth-btn ${currentExternalOrderId ? 'block' : 'active'}`}
            type="button"
            value="取消會員"
            onClick={onCancelMemberClick}
          />
        ) : (
          <input
            className={`smooth-btn ${currentExternalOrderId ? 'block' : ''}`}
            type="button"
            value="&emsp;會員&emsp;"
            onClick={onMemberClick}
          />
        )
        }

        <Toggle determineParam={memberOpen}>
          <DialogMember
            open={memberOpen}
            onSelectClick={onSelectMemberClick}
            handleClose={() => setMemberOpen(false)}
          />
        </Toggle>

        {currentTempOrderId ? (
          <input
            className="smooth-btn active"
            type="button"
            value="取消暫存"
            onClick={onCancelTempLoadClick}
          />
        ) : (
          <input
            className={`smooth-btn ${currentExternalOrderId ? 'block' : ''}`}
            type="button"
            value="選取暫存"
            onClick={onTempLoadClick}
          />
        )
        }

        <Toggle determineParam={tempLoadOpen}>
          <DialogTempOrder
            open={tempLoadOpen}
            handleClose={() => setTempLoadOpen(false)} />
        </Toggle>

        <input
          className={`smooth-btn ${currentExternalOrderId ? 'block' : ''}`}
          type="button"
          value="加入暫存"
          onClick={onTempCreateClick}
        />

      </div>
    </Box>
  )
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    posStrips: state.posStrips,
    posStripsSum: state.posStripsSum,
    currentMember: state.currentMember,
    currentSalesmanId: state.currentSalesmanId,
    currentTempOrderId: state.currentTempOrderId,
    currentExternalOrderId: state.currentExternalOrderId,
  }
}

export default connect(
  mapStateToProps,
  {
    addTempOrderWithLocal,
    replacePosStrips,
    replaceCurrentTempOrderId,
    replaceCurrentMember,
    replaceCurrentExternalOrderId,
    replaceCurrentSalesmanId,
  }
)(ControlBar)