import { MULTIPLE_MODE } from '../publicVariable'

// -驗證折扣字串-
export const verifyDiscountString = (discountString) => {
  // const percentTest = new RegExp('^\\d+%$').test(discountString)
  const percentTest = new RegExp('^%\\d{0,2}$').test(discountString)
  const amountTest = new RegExp('^\\d*\\d$').test(discountString)
  if (percentTest) return 'percentage'
  else if (amountTest) return 'constant'
  return ''
}

// -計算折扣額度-
export const convertDiscount = (comm, mode, discountString) => {
  let discountAmount = 0
  if (mode === 'percentage') {
    // discountAmount = (Number(discountString.slice(0, -1)) * comm.price) / 100
    const newDiscountString = discountString.replace('%', '.')
    const discountNum = Number(newDiscountString)
    if (!Number.isNaN(discountNum)) {
      discountAmount = (1 - discountNum) * comm.price
    }
  }
  if (mode === 'constant') {
    discountAmount = Number(discountString)
  }

  return discountAmount
}

// -品項總和計算-
export const calcCommTotal = comm => {
  let total = comm.price * comm.quantity
  if (comm.discount) {
    const discountMode = verifyDiscountString(comm.discount)
    const discountAmount = convertDiscount(comm, discountMode, comm.discount)
    const discountMultiple =
      discountMode === 'percentage' || MULTIPLE_MODE === 'single'
        ? comm.quantity
        : 1
    // total -= Math.ceil(discountAmount * discountMultiple)
    total = Math.ceil(total - (discountAmount * discountMultiple))
  }
  return {
    ...comm,
    originalTotal: comm.price * comm.quantity,
    salePrice: total / comm.quantity,
    total,
  }
}