import React, { useState, useEffect, useMemo } from 'react'
import { connect } from 'react-redux'
import { Box, Typography } from '@mui/material'
import { getNewStrip } from '../../helpers/checkout'
import { addPosStrip } from '../../slices/posStripsSlice'
import { fetchPriceMapping } from '../../slices/priceMappingSlice'
import { fetchPosLayouts } from '../../slices/posLayoutsSlice'

const CommGrid = ({
  listRef,
  addPosStrip,
  fetchPriceMapping,
  fetchPosLayouts,
  setActiveCommId,
  setUniformContent,
  posLayouts,
  priceMapping,
  posLayoutsStatus,
}) => {
  const [currentLayoutIndex, setCurrentLayoutIndex] = useState(0)

  useEffect(() => {
    fetchPosLayouts()
  }, [fetchPosLayouts])

  const posLayout = useMemo(() => {
    return posLayouts[currentLayoutIndex]
  }, [currentLayoutIndex, posLayouts])

  useEffect(() => {
    if (posLayoutsStatus !== 'idle') return
    fetchPriceMapping(posLayout.stockList)
  }, [posLayout, posLayoutsStatus, fetchPriceMapping])

  const onCommClick = async (comm) => {
    if (!comm.stockName) return
    const newStrip = getNewStrip(comm, priceMapping)
    await addPosStrip(newStrip)
    setUniformContent('')
    setActiveCommId(newStrip.id)
    scrollToBottom(listRef.current)
  }

  const onTagClick = index => {
    setCurrentLayoutIndex(index)
  }

  const scrollToBottom = el => {
    el.scrollTo(0, el.scrollHeight)
  }

  const renderedGridCards = cards => {
    return cards.map((card, index) => {
      const stockPrice = priceMapping[card.stockId]
      return (
        <div
          key={index}
          className="comm-grid-card"
          style={{ backgroundColor: card.backgroundColor }}
          onClick={() => onCommClick(card)}>
          <div className="name-field">
            <span>{card.stockName}</span>
          </div>
          <div className="price-field">
            <Typography>
              {(stockPrice ? '$' + stockPrice : ' ')}
            </Typography>
          </div>
        </div>
      )
    })
  }

  const renderedTags = tags => {
    return tags.map((tag, index) => {
      const active = currentLayoutIndex === index ? 'active' : '';
      return (
        <div
          className={`template-tag ${active}`}
          onClick={() => onTagClick(index)}
          key={tag.id}>
          {tag.categoryName}
        </div>
      )
    })
  }

  return (
    <React.Fragment>
      <Box component="section" id="comm-grid" sx={styles.commGrid}>
        <div className="grid-container">
          {renderedGridCards(posLayout.stockList)}
        </div>
      </Box>
      <Box component="section" id="template-tag-list" sx={styles.templateTagList}>
        {renderedTags(posLayouts)}
      </Box>
    </React.Fragment>
  )
}

const styles = {
  commGrid: {
    flex: '0 0 90.5%',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    flexDirection: 'column',
    '& .grid-container': {
      flex: '0 0 95%',
      display: 'flex',
      justifyContent: 'space-around',
      flexWrap: 'wrap',
      width: '87.5%',
      margin: '0 auto',
      minHeight: 0,
    },
    '& .comm-grid-card': {
      flex: '0 0 31.33333%',
      display: 'inline-flex',
      flexDirection: 'column',
      color: '#666666',
      backgroundColor: '#fcfcfc',
      borderRadius: '0.3em',
      fontWeight: 'bold',
      width: '31.33333%',
      margin: '1% 0',
      padding: '0 0.5em',
      cursor: 'pointer',
      userSelect: 'none',
    },
    '& .name-field': {
      flex: '0 0 75%',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      textAlign: 'center',
      fontSize: '0.875rem',
      overflow: 'auto',
      '& span': {
        display: 'inline-flex',
        justifyContent: 'center',
        textOverflow: 'ellipsis',
        maxHeight: '3em',
        textShadow: theme => `0px 0px 3px ${theme.palette.background.paper}`,
      },
      '&::-webkit-scrollbar': {
        width: '1px',
        height: '1px',
      },
      '&::-webkit-scrollbar-track': {
        background: 'rgba(100, 100, 100, 0)',
      },
      '&::-webkit-scrollbar-thumb': {
        background: 'rgba(100, 100, 100, 0.04)',
      },
    },
    '& .price-field': {
      flex: '0 0 25%',
      display: 'flex',
      alignItems: 'center',
      textAlign: 'center',
      wordBreak: 'break-all',
      letterSpacing: '0.03em',
      fontSize: '1rem',
      fontWeight: 'normal',
      color: '#707070',
      padding: '0 0.5em',
      borderTop: '1px solid #eeeeee',
      '& .MuiTypography-root': {
        minHeight: '1.5em',
      },
    },
  },
  templateTagList: {
    flex: '0 0 7.5%',
    display: 'flex',
    backgroundColor: '#fcfcfc',
    overflow: 'auto',
    minHeight: 0,
    '& .template-tag': {
      flex: '0 0 20%',
      position: 'relative',
      display: 'inline-flex',
      justifyContent: 'center',
      alignItems: 'center',
      textAlign: 'center',
      fontWeight: 'bold',
      color: '#777777',
      cursor: 'pointer',
      userSelect: 'none',
      padding: '0 0.5em',
      '&::before': {
        content: '""',
        position: 'absolute',
        height: '1em',
        width: '1px',
        left: 0,
        borderLeft: '2px solid #eeeeee',
      },
      '&.active': {
        color: 'background.default',
        background: (theme) => theme.palette.primary.light,
      },
      '&:first-of-type::before': {
        content: 'none',
      },
      '&.active::before': {
        content: 'none',
      },
      '&.active + .template-tag::before': {
        content: 'none',
      }
    },
  },
}

const mapStateToProps = state => {
  return {
    priceMapping: state.priceMapping.content,
    posLayouts: state.posLayouts.content,
    posLayoutsStatus: state.posLayouts.loading,
  }
}

export default connect(
  mapStateToProps,
  { addPosStrip, fetchPriceMapping, fetchPosLayouts }
)(CommGrid)