import React from 'react'
import { connect } from 'react-redux'
import { Box } from '@mui/material'
import { replacePosStrips } from '../../slices/posStripsSlice'
import { useTheme, alpha } from '@mui/system'
import { Toast } from '../../helpers/sweetAlert'
import { verifyDiscountString, convertDiscount, calcCommTotal } from '../../helpers/discountCalc'

const NumberPanel = ({
  posStrips, replacePosStrips,
  uniformContent, setUniformContent,
  keyboardMode, setKeyboardMode,
  activeCommId, selectedIds,
}) => {
  const theme = useTheme()

  const onPanelItemClick = e => {
    onUniformContentChange(uniformContent + e.target.dataset.text, keyboardMode)
  }

  const onPanelBackClick = () => {
    if (uniformContent.length === 0) return
    onUniformContentChange(uniformContent.slice(0, -1), keyboardMode)
  }

  const onKeyboardModeClick = e => {
    setKeyboardMode(e.target.dataset.keyboardMode)
    setUniformContent('')
  }

  const onUniformContentChange = (newValue, keyboardMode) => {
    if (selectedIds.length <= 0 && !activeCommId) return Toast.fire({ title: '提醒: 請先選擇商品', target: document.querySelector('.right-platform') })
    const isCommsChecked = (selectedIds.length > 0)
    let newComms = []
    switch (keyboardMode) {
      case 'discountMode':
        newComms = isCommsChecked ? handelDiscountUnify(newValue) : handleDiscountModify(newValue)
        break
      case 'quantityMode':
        if (isIllegalQuantityStr(newValue)) return
        setUniformContent(newValue);
        newComms = isCommsChecked ? handleQuantityUnify(newValue) : handleQuantityModify(newValue)
        break
      default:
        newComms = [...posStrips]
    }
    replacePosStrips(newComms)
  };

  // -勾選多筆折扣處理-
  const handelDiscountUnify = newValue => {
    const discountMode = verifyDiscountString(newValue);
    if (!discountMode && newValue !== '') return posStrips
    const newComms = []
    let isDiscountExcess = false
    let isContainUndiscountable = false
    posStrips.findIndex(strip => {
      if (selectedIds.includes(strip.id)) {
        const discountAmount = convertDiscount(strip, discountMode, newValue)
        if (strip.undiscountable) {
          isContainUndiscountable = true
          Toast.fire({
            title: '警告: 包含無法折扣的商品',
            target: document.querySelector('.right-platform')
          })
          return true
        }
        if ((strip.price * strip.quantity) < discountAmount) {
          isDiscountExcess = true;
          Toast.fire({
            title: '警告: 折扣額度大於部分商品',
            target: document.querySelector('.right-platform')
          })
          return true
        }
        newComms.push(calcCommTotal({ ...strip, discount: newValue }))
        return false
      }
      else {
        newComms.push(strip);
        return false
      }
    })
    if (isContainUndiscountable || isDiscountExcess) { return posStrips }
    else {
      setUniformContent(newValue)
      return newComms
    }
  }

  // -勾選單筆折扣處理-
  const handleDiscountModify = newValue => {
    const discountMode = verifyDiscountString(newValue)
    if (!discountMode && newValue !== '') return posStrips
    return posStrips.map(strip => {
      if (strip.id === activeCommId) {
        if (strip.undiscountable) {
          Toast.fire({
            title: '警告: 此為不可折扣的商品',
            target: document.querySelector('.right-platform')
          })
          return strip
        }
        const discountAmount = convertDiscount(strip, discountMode, newValue)
        if ((strip.price * strip.quantity) >= discountAmount) {
          setUniformContent(newValue)
          return calcCommTotal({ ...strip, discount: newValue })
        }
        else {
          Toast.fire({
            title: '警告: 折扣額度大於部分商品',
            target: document.querySelector('.right-platform')
          })
          return strip
        }
      }
      else { return strip }
    })
  }

  // -勾選多筆數量處理-
  const handleQuantityUnify = newValue => {
    return posStrips.map(strip => {
      return selectedIds.includes(strip.id)
        ? calcCommTotal({ ...strip, quantity: Number(newValue) || 1 })
        : strip
    })
  }

  // -勾選單筆數量處理-
  const handleQuantityModify = newValue => {
    return posStrips.map(strip => {
      return strip.id === activeCommId
        ? calcCommTotal({ ...strip, quantity: Number(newValue) || 1 })
        : strip
    })
  }

  // -檢查是否為合法數字字串-
  const isIllegalQuantityStr = inputString => {
    return (inputString !== '' && (isNaN(inputString) || Number(inputString) <= 0))
  }

  return (
    <Box component="section" id="number-panel" sx={{
      flex: '0 0 50%',
      display: 'flex',
      alignItems: 'flex-end',
      justifyContent: 'center',
      flexDirection: 'column',
      '& .number-panel-container': {
        flex: '0 0 85%',
        display: 'flex',
        width: '100%',
      },
    }}>
      <div className="number-panel-container">

        <Box className="left-panel" sx={{
          flex: '0 0 90%',
          display: 'flex',
          flexDirection: 'column',
          borderRight: theme => `1px solid ${theme.palette.background.paper}`,
          '& .current-input': {
            flex: '0 0 17.5%',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            fontSize: '1.25rem',
            fontWeight: 'bold',
            userSelect: 'none',
            color: '#cccccc',
            bgcolor: theme => alpha(theme.palette.secondary.main, 0.86),
          },
        }}>
          <div className="current-input">
            {uniformContent || '點擊數字面板'}
          </div>
          <Box className="panel-item-list" sx={{
            flex: '0 0 82.5%',
            display: 'flex',
            flexWrap: 'wrap',
            '& .panel-item': {
              flex: '0 0 25%',
              display: 'inline-flex',
              alignItems: 'center',
              justifyContent: 'center',
              fontSize: '1.25rem',
              fontWeight: 'bold',
              color: 'background.paper',
              cursor: 'pointer',
              userSelect: 'none',
              borderRight: theme => `1px solid ${theme.palette.background.paper}`,
              borderTop: theme => `1px solid ${theme.palette.background.paper}`,
              bgcolor: theme => alpha(theme.palette.secondary.main, 0.86),
              '&.orange': {
                color: theme.palette.primary.main,
              },
              '&:nth-last-of-type(4n + 1)': {
                borderRight: 'none'
              },
            },
          }}>
            <div className="panel-item" data-text="1" onClick={onPanelItemClick}>1</div>
            <div className="panel-item" data-text="2" onClick={onPanelItemClick}>2</div>
            <div className="panel-item" data-text="3" onClick={onPanelItemClick}>3</div>
            <div className="panel-item orange" data-text="-" onClick={onPanelBackClick}>←</div>
            <div className="panel-item" data-text="4" onClick={onPanelItemClick}>4</div>
            <div className="panel-item" data-text="5" onClick={onPanelItemClick}>5</div>
            <div className="panel-item" data-text="6" onClick={onPanelItemClick}>6</div>
            <div className="panel-item" data-text="%" onClick={onPanelItemClick}>%</div>
            <div className="panel-item" data-text="7" onClick={onPanelItemClick}>7</div>
            <div className="panel-item" data-text="8" onClick={onPanelItemClick}>8</div>
            <div className="panel-item" data-text="9" onClick={onPanelItemClick}>9</div>
            <div className="panel-item" data-text="0" onClick={onPanelItemClick}>0</div>
          </Box>
        </Box>

        <Box className="right-panel" sx={{
          flex: '0 0 10%',
          display: 'flex',
          justifyContent: 'flex-end',
          flexDirection: 'column',
          borderLeft: 'none',
          '& .panel-type': {
            flex: ' 0 0 50%',
            display: 'flex',
            textAlign: 'center',
            alignItems: 'center',
            justifyContent: 'center',
            fontWeight: 'bold',
            color: '#c4c4c4',
            cursor: 'pointer',
            userSelect: 'none',
            border: '1px solid #c4c4c4',
            bgcolor: 'background.paper',
            padding: '0 0.5em',
            '&:hover': {
              bgcolor: alpha(theme.palette.text.secondary, 0.01),
            },
            '&:last-of-type': {
              borderTop: 'none',
            },
            '&.active': {
              color: 'background.paper',
              borderColor: theme.palette.primary.main,
              bgcolor: theme.palette.primary.main,
              '&:hover': {
                bgcolor: alpha(theme.palette.primary.main, 0.8)
              },
            },
          },
        }}>
          <div
            className={`panel-type ${keyboardMode === 'quantityMode' ? 'active' : ''}`}
            data-keyboard-mode="quantityMode"
            onClick={onKeyboardModeClick}>
            數量
          </div>
          <div
            className={`panel-type ${keyboardMode === 'discountMode' ? 'active' : ''}`}
            data-keyboard-mode="discountMode"
            onClick={onKeyboardModeClick}>
            折扣
          </div>
        </Box>
      </div>
    </Box>
  )
}

const mapStateToProps = state => {
  return {
    posStrips: state.posStrips
  }
}

export default connect(
  mapStateToProps,
  { replacePosStrips }
)(NumberPanel)