import React, { useState, useEffect, useMemo } from 'react'
import { connect } from 'react-redux'

import Box from '@mui/material/Box'
import Grid from '@mui/material/Grid'
import Dialog from '@mui/material/Dialog'
import Button from '@mui/material/Button'
import Divider from '@mui/material/Divider'
import IconButton from '@mui/material/IconButton'
import Typography from '@mui/material/Typography'
import DialogContent from '@mui/material/DialogContent'
// import DialogActions from '@mui/material/DialogActions'
import OrderList from '../../ui/OrderList'

import Cancel from '@mui/icons-material/Cancel'
import { fetchSales } from '../../../slices/salesSlice'

const DialogRepay = ({
  sales,
  fetchSales,
  open,
  handleClose,
}) => {
  const [currentOrderId, setCurrentOrderId] = useState('')

  const currentPaidOrder = useMemo(() => {
    return (sales.list && sales.list.find(order => order.id === currentOrderId)) || {}
  }, [sales, currentOrderId])

  useEffect(() => {
    fetchSales({
      isReturn: 0,
    })
  }, [fetchSales])

  useEffect(() => {
    if (sales.list && sales.list[0]) setCurrentOrderId(sales.list[0].id);
  }, [sales])

  return (
    <Dialog
      id="dialog-repay"
      aria-labelledby="dialog-repay"
      fullWidth
      maxWidth="md"
      open={open}
      onClose={handleClose}
    >
      <Box
        sx={{
          flex: '0 0 auto',
          margin: 0,
          padding: '0.375em 1.25em 0.25em',
          lineHeight: 1.6,
          letterSpacing: '0.0075rem',
          bgcolor: 'background.default',
          '& h6': {
            color: '#3e414f',
            fontSize: '1.375rem',
            fontWeight: 'bold',
            letterSpacing: '0.05em',
          }
        }}
      >
        <Typography variant="h6">訂單還款</Typography>
        {handleClose ? (
          <IconButton aria-label="close" onClick={handleClose} sx={{
            position: 'absolute',
            right: '0.5em',
            top: '0.25em',
            color: '#cb5c5c',
            padding: 0,
          }}>
            <Cancel sx={{ width: '1.25em', height: '1.25em' }} />
          </IconButton>
        ) : null}
      </Box>

      <DialogContent
        sx={{
          padding: '0.25em 1.75em 0',
          backgroundColor: '#fafafa',
          '& .search-result': {
            overflowY: 'auto',
          },
        }}
      >

        <Grid container alignItems="center" justifyContent="center">
          <Grid item container alignItems="center" justifyContent="space-between"
            sx={{
              pt: '1.25rem',
              pb: '1.5rem',
              flex: '0 0 95%',
            }}
          >

            <Grid
              item
              xs={6}
              display="inline-flex"
              className="received-amount-input"
              sx={{
                '& input': {
                  flex: '0 0 80%',
                  width: '100%',
                  lineHeight: 2,
                  textAlign: 'center',
                  fontSize: '1.25rem',
                  fontWeight: 'bold',
                  letterSpacing: '0.02em',
                  border: '1px solid #aaaaaa',
                  borderRight: 'none',
                  borderTopLeftRadius: '0.25em',
                  borderBottomLeftRadius: '0.25em',
                  color: (theme) => theme.palette.primary.main,
                  outlineColor: (theme) => theme.palette.primary.main,
                  backgroundColor: 'background.paper',
                  '&::placeholder': {
                    color: '#c4c4c4',
                  },
                },
                '& button': {
                  flex: '0 0 20%',
                  textAlign: 'center',
                  fontSize: '1rem',
                  fontWeight: 'bold',
                  letterSpacing: '0.025em',
                  border: (theme) => `1px solid ${theme.palette.primary.main}`,
                  borderLeft: 'none',
                  borderTopLeftRadius: 0,
                  borderBottomLeftRadius: 0,
                  color: 'background.default',
                  backgroundColor: (theme) => theme.palette.primary.main,
                  '&:hover': {
                    backgroundColor: '#fabd5e',
                  },
                  '&:disabled': {
                    borderColor: '#aaaaaa',
                  }
                },
              }}
            >
              <input
                placeholder="輸入還款金額"
              />
              <Button
                variant="contained"
                disableElevation
              >
                填入
              </Button>
            </Grid>

            <Grid
              item
              xs={6}
              className="main-payment-method"
              display="inline-flex"
              sx={{
                pl: '2rem',
                '& button': {
                  flex: '0 0 47.5%',
                  fontSize: '1.125rem',
                  fontWeight: 'bold',
                  color: 'background.paper',
                  backgroundColor: 'secondary.matte',
                  '&:first-of-type': {
                    mr: '5%',
                  },
                  '&:hover': {
                    backgroundColor: '#606c9a',
                  },
                },
              }}
            >
              <Button variant="contained">
                現金
              </Button>
              <Button variant="contained">
                匯款
              </Button>
            </Grid>

          </Grid>
        </Grid>

        <Divider sx={{ mb: '1.25rem' }} />


        <OrderList
          sales={sales}
          fetchSales={fetchSales}
          currentOrderId={currentOrderId}
          setCurrentOrderId={setCurrentOrderId}
          currentPaidOrder={currentPaidOrder}
        />


      </DialogContent>
    </Dialog>
  )
}

const mapStateToProps = state => {
  return {
    sales: state.sales,
  }
}

export default connect(
  mapStateToProps,
  {
    fetchSales,
  }
)(DialogRepay)