import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import { fetchMembers } from '../../slices/membersSlice'
import { memberRequest } from '../../apis'
import CancelIcon from '@mui/icons-material/Cancel'
import { fireDialogToast } from '../../helpers/sweetAlert'
import { Button, Dialog, DialogContent, IconButton, Typography, Box } from '@mui/material'

const MemberDialog = ({
  auth,
  open,
  members,
  handleClose,
  fetchMembers,
  onSelectClick,
}) => {
  const [memberFormInfo, setMemberFormInfo] = useState({})
  const [validateErrors, setValidateError] = useState({})

  useEffect(() => {
    if (!open) return
    fetchMembers()
  }, [open, fetchMembers])

  const handleInputChange = e => {
    const { name, value } = e.target
    const newValue = { ...memberFormInfo, [name]: value }
    setMemberFormInfo(newValue)
  }

  const handleSubmit = e => {
    e.preventDefault()
    fetchMembers(memberFormInfo)
    setValidateError({})
  }

  const handleCreate = async e => {
    e.preventDefault()
    if (validateCreate()) return

    try {
      const data = {
        memberName: memberFormInfo.name,
        phone: memberFormInfo.phone,
      }
      const response = await memberRequest.post('', data, {
        headers: { 'Authorization': 'Bearer ' + auth.authenticated },
      })
      console.log(response)

      fetchMembers()
      setMemberFormInfo({})
      fireDialogToast('新增會員成功', 'success')
    } catch (error) {
      fireDialogToast(`錯誤: ${error?.response?.data?.error || '新增會員失敗'}`, 'error')
    }
  }

  const validateCreate = () => {
    const errors = {}
    if (!memberFormInfo.phone) {
      errors.phone = '電話為必填'
    } else if (memberFormInfo.phone[0] !== '0') {
      errors.phone = '電話格式錯誤'
    }
    if (!memberFormInfo.name) {
      errors.name = '姓名為必填'
    }
    setValidateError(errors)
    return (Object.keys(errors).length > 0)
  }

  const renderList = (list) => {
    return list.map(item => {
      return (
        <div className="list-item" key={item.id}>
          <div className="id-sec">
            <Typography variant="body1" noWrap={true}>
              {item.memberCode || '-'}
            </Typography>
          </div>
          <div className="name-sec">
            <Typography variant="body1" noWrap={true} title={item.memberName}>
              {item.memberName}
            </Typography>
          </div>
          <div className="mobile-sec">
            <Typography variant="body1" noWrap={true}>
              {item.phone || '-'}
            </Typography>
          </div>
          <div className="tel-sec">
            <Typography variant="body1" noWrap={true}>
              {item.homePhone || '-'}
            </Typography>
          </div>
          <div className="select-sec" onClick={() => onSelectClick(item)}>
            <Button sx={{
              minWidth: '3.5em',
              height: '1.75em',
              padding: '0',
              color: 'background.paper',
              bgcolor: (theme) => theme.palette.primary.main,
              '&:hover': {
                bgcolor: '#fabd5e',
              },
            }}>
              選取
            </Button>
          </div>
        </div>
      )
    }
    )
  }

  return (
    <Dialog
      id="dialog-member"
      open={open}
      onClose={handleClose}
      aria-labelledby="dialog-member"
      fullWidth maxWidth="sm"
      PaperProps={{
        sx: { maxWidth: 700 }
      }}
    >
      <Box
        sx={{
          margin: 0,
          padding: '0.375em 1.75em 0.25em',
          bgcolor: 'background.default',
          '& h6': {
            color: 'secondary.main',
            fontSize: '1.25rem',
            fontWeight: 'bold',
          }
        }}
      // sx={{
      //   flex: '0 0 auto',
      //   margin: 0,
      //   padding: '0.375em 1.25em 0.25em',
      //   lineHeight: 1.6,
      //   letterSpacing: '0.0075rem',
      //   '& h6': {
      //     color: (theme) => theme.palette.primary.main,
      //     fontSize: '1.375rem',
      //     fontWeight: 'bold',
      //     letterSpacing: '0.05em',
      //   }
      // }}
      >
        <Typography variant="h6">會員選取</Typography>
        {handleClose ? (
          <IconButton aria-label="close" onClick={handleClose} sx={{
            position: 'absolute',
            right: '0.5em',
            top: '0.25em',
            color: '#cb5c5c',
            padding: 0,
          }}>
            <CancelIcon sx={{ width: '1.25em', height: '1.25em' }} />
          </IconButton>
        ) : null}
      </Box>

      <DialogContent sx={{ padding: '0.25em 1.75em 2em', bgcolor: 'background.paper' }}>
        <Box component="form" sx={formFieldStyle}>
          <div className="input-field">

            <div className="input-wrapper">
              <label>姓名 : &nbsp;</label>
              <input
                className={`input-md ${validateErrors.name ? 'error' : ''}`}
                type="text"
                name="name"
                value={memberFormInfo.name || ''}
                onChange={handleInputChange}
                autoComplete="off"
              />
              {validateErrors.name && (<span>{validateErrors.name}</span>)}
            </div>

            <div className="input-wrapper">
              <label>電話 : &nbsp;</label>
              <input
                className={`input-md ${validateErrors.phone ? 'error' : ''}`}
                type="text"
                name="phone"
                value={memberFormInfo.phone || ''}
                onChange={handleInputChange}
                autoComplete="off"
              />
              {validateErrors.phone && (<span>{validateErrors.phone}</span>)}
            </div>



            <div className="input-wrapper">
              <label>寵物名字 : &nbsp;</label>
              <input
                className={`input-sm ${validateErrors.petName ? 'error' : ''}`}
                type="text"
                name="petName"
                value={memberFormInfo.petName || ''}
                onChange={handleInputChange}
              />
            </div>
          </div>

          <div className="submit-field">
            <Button
              className="submitBtn"
              type="submit"
              onClick={handleSubmit}
            >
              搜尋
            </Button>
            <Button
              className="newBtn"
              variant="contained"
              color="primary"
              disableElevation
              onClick={handleCreate}
            >
              新增
            </Button>
          </div>
        </Box>

        {/* <Box sx={{
          padding: '0.25em 0 0.5em',
          '& button': {
            fontSize: '1rem',
            fontWeight: 'bold',
            padding: '0 0.5em',
            marginRight: '0.5em',
            borderRadius: 0,
            color: 'text.secondary',
            borderColor: 'text.secondary',
          },
        }}>
          <Button variant="outlined">最近交易會員</Button>
          <Button variant="outlined">未結批價單會員</Button>
        </Box> */}

        <div>
          <Box sx={{
            display: 'flex',
            bgcolor: 'secondary.main',
            '& > div': {
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              fontSize: '1rem',
              borderRight: theme => `1px solid ${theme.palette.background.darkPaper}`,
              color: 'background.paper',
              height: '1.75em',
            },
            '& .id-sec': {
              flex: '0 0 17.5%',
            },
            '& .name-sec': {
              flex: '0 0 17.5%',
            },
            '& .mobile-sec': {
              flex: '0 0 25%',
            },
            '& .tel-sec': {
              flex: '0 0 25%',
            },
            '& .select-sec': {
              flex: '0 0 15%',
              borderRight: 'none',
            },
          }}>
            <div className="id-sec">代碼</div>
            <div className="name-sec">會員名稱</div>
            <div className="mobile-sec">手機號碼</div>
            <div className="tel-sec">住家號碼</div>
            <div className="select-sec"></div>
          </Box>
          <Box sx={{
            maxHeight: '14rem',
            overflow: 'overlay',
            '& .list-item': {
              display: 'flex',
              '& > div': {
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                fontSize: '1rem',
                borderRight: theme => `1px solid ${theme.palette.background.darkPaper}`,
                color: 'secondary.main',
                height: '2em',
                overflow: 'hidden',
                padding: '0 0.5em',
              },
              '&:nth-of-type(2n + 1)': {
                bgcolor: '#ececec',
              },
              '& .id-sec': {
                flex: '0 0 17.5%',
              },
              '& .name-sec': {
                flex: '0 0 17.5%',
              },
              '& .mobile-sec': {
                flex: '0 0 25%',
              },
              '& .tel-sec': {
                flex: '0 0 25%',
              },
              '& .select-sec': {
                flex: '0 0 15%',
                borderRight: 'none',
                overflow: 'hidden',
              },
            },
          }}>
            {renderList(members)}
          </Box>
        </div>
      </DialogContent>
    </Dialog>
  )
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    members: state.members,
  }
}

const formFieldStyle = {
  display: 'flex',
  padding: '0.5em 0 0.75em',
  '& .input-field': {
    flex: '0 0 85%',
    color: 'secondary.main',
    paddingRight: '0.5em',
    display: 'flex',
    flexWrap: 'wrap',
  },
  '& .input-wrapper': {
    flex: '0 0 50%',
    py: '0.375em',
    pr: '0.5em',
    display: 'flex',
    alignItems: 'center',
    flexWrap: 'wrap',
    position: 'relative',
    '& span': {
      color: 'error.light',
      fontSize: '0.75rem',
      position: 'absolute',
      bottom: '0',
      transform: 'translateY(50%)',
    },
  },
  '& label': {
    fontWeight: 'bold',
    fontSize: '1.125rem',
  },
  '& input': {
    flexGrow: 1,
    lineHeight: 1.25,
    padding: '0 0.25em',
    '&::placeholder': {
      fontSize: '0.875rem',
      letterSpacing: '0.04em',
    },
    '&.error::placeholder': {
      color: 'error.light',
    },
  },
  '& input.input-md': {
    width: 'calc(100% - 4em)',
  },
  '& input.input-sm': {
    width: 'calc(100% - 6.5em)',
  },
  '& .submit-field': {
    flex: '0 0 15%',
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'center',
    '& .submitBtn': {
      flex: '0 0 100%',
      color: 'background.paper',
      fontWeight: 'bold',
      fontSize: '1.125rem',
      display: 'inline-flex',
      justifyContent: 'center',
      bgcolor: (theme) => theme.palette.primary.main,
      // margin: '0.75em 0',
      borderRadius: '0.5em',
      borderBottomLeftRadius: 0,
      borderBottomRightRadius: 0,
      border: 'none',
      '&:hover': {
        // bgcolor: '#fabd5e',
        bgcolor: (theme) => theme.palette.primary.light,
      },
    },
    '& .newBtn': {
      flex: '0 0 100%',
      lineHeight: 1.5,
      color: 'background.paper',
      fontWeight: 'bold',
      borderRadius: '0.5em',
      borderTopLeftRadius: 0,
      borderTopRightRadius: 0,
      bgcolor: '#6aabc3',
      '&:hover': {
        bgcolor: '#72b9d3',
      },
    },
  },
}

export default connect(
  mapStateToProps,
  { fetchMembers }
)(MemberDialog)