// 取得前一天或後一天的 Date Input 字串
export const getDateString = (dateString, changeType) => {
  const originMs = new Date(dateString).getTime()
  const newMs = changeType === 'previous' ? (originMs - 86400000) : (originMs + 86400000)
  return parseMsToDateString(newMs)
}

export const parseMsToDateString = (ms, divider = '-') => {
  const originDate = new Date(ms)
  const monthNumber = originDate.getMonth() + 1
  const monthString = monthNumber.toString().padStart(2, '0')
  // const monthString = monthNumber < 10 ? ('0' + monthNumber.toString()) : monthNumber.toString()
  const dateNumber = originDate.getDate()
  const dateString = dateNumber.toString().padStart(2, '0')
  // const dateString = dateNumber < 10 ? ('0' + dateNumber.toString()) : dateNumber.toString()
  return (originDate.getFullYear().toString() + divider + monthString + divider + dateString)
}