import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import { useTheme, alpha } from '@mui/system'
import { Box, Button, SvgIcon } from '@mui/material'
import SwapVerticalCircle from '@mui/icons-material/SwapVerticalCircle'
import PointOfSale from '@mui/icons-material/PointOfSale'
// import PlayForWork from '@mui/icons-material/PlayForWork'
import { clearPosStrips } from '../../slices/posStripsSlice'
import { replaceCreditCardResponse } from '../../slices/creditCardResponseSlice'
import { Toast } from '../../helpers/sweetAlert'
import useBarcode from '../../hooks/useBarcode'
import DialogCheckout from './Dialog/DialogCheckout'
import DialogReturn from './Dialog/DialogReturn'
import DialogRepay from './Dialog/DialogRepay'
import Toggle from '../Toggle'

const SettlementBar = ({
  posStrips,
  setSelectedIds,
  clearPosStrips,
  receivedAmounts,
  setUniformContent,
  replaceCreditCardResponse,
}) => {
  const theme = useTheme()
  const [checkoutOpen, setCheckoutOpen] = useState(false)
  const [returnOpen, setReturnOpen] = useState(false)
  const [repayOpen, setRepayOpen] = useState(false)
  const [isPartialClear, setIsPartialClear] = useState(false)
  const [barcode, clearBarcode] = useBarcode()

  useEffect(() => {
    if (!barcode || checkoutOpen) return
    console.log(barcode)
    clearBarcode()

  }, [barcode, clearBarcode, checkoutOpen])

  const onClearStripsClick = () => {
    clearPosStrips()
    setUniformContent('')
    setSelectedIds([])
  }

  const onReturnClick = () => {
    setReturnOpen(true)
  }

  const onReturnClose = () => {
    setReturnOpen(false)
  }

  // const onRepayClick = () => {
  //   setRepayOpen(true)
  // }

  const onRepayClose = () => {
    setRepayOpen(false)
  }

  const onMoneyClick = () => {
    // 在這裡打開**收銀機**

    // 以下為測試用
    // console.log('window.configBridge.getConfig()?.printer', window.configBridge.getConfig()?.printer)
    // window.configBridge.setConfig('edc', { path: 'COM3' })
    // console.log('window.configBridge.getConfig()?.edc', window.configBridge.getConfig()?.edc)
    // console.log('----------')

    // console.log('信用卡:')
    // console.log(
    //   'window.edcBridge.send({ amount: 11, edc: window.configBridge.getConfig()?.edc })',
    //   window.edcBridge.send({ amount: 11, edc: window.configBridge.getConfig()?.edc })
    // )

    // console.log('收銀機:')
    // console.log(
    //   'window.printerBridge.openCashRegister()',
    //   window.printerBridge.openCashRegister()
    // )

    window.printerBridge.openCashRegister()
  }

  const onCheckoutClick = () => {
    if (posStrips.length < 1) return Toast.fire({
      title: "提醒: 未有能結帳的商品",
      target: document.querySelector(".right-platform"),
    })
    setCheckoutOpen(true)
  }

  const handleCheckoutClose = () => {
    setIsPartialClear(false)
    setCheckoutOpen(false)
    replaceCreditCardResponse({})
  }

  const handleValidateAndClose = () => {
    if (!validateReceived()) return
    handleCheckoutClose()
  }

  const validateReceived = () => {
    if (Boolean(Object.keys(receivedAmounts).length)) {
      Toast.fire({
        title: `請先「結帳」或「清除付款」`,
        target: document.querySelector('.MuiDialog-root')
      })
      return false
    }
    return true
  }

  return (
    <Box component="section" id="settlement-bar"
      sx={{
        flex: '0 0 9.125%',
        display: 'flex',
        alignItems: 'flex-start',
        justifyContent: 'space-between',
        '& .btn-smooth': {
          borderRadius: '0.5em',
          color: 'background.paper',
          fontWeight: 'bold',
          fontSize: '1.25rem',
        },
        '& .btn-clear': {
          backgroundColor: '#cb5c5c',
          '&:hover': {
            bgcolor: alpha('#cb5c5c', 0.8),
          },
        },
        '& .btn-return': {
          backgroundColor: '#52483b',
          marginLeft: '1em',
          '&:hover': {
            bgcolor: alpha('#52483b', 0.8),
          },
        },
        '& .btn-money': {
          backgroundColor: 'secondary.main',
          marginLeft: '1em',
          '&:hover': {
            bgcolor: theme => alpha(theme.palette.secondary.main, 0.8),
          },
          '& svg': {
            color: theme.palette.primary.main,
          },
        },
        '& .btn-settlement': {
          backgroundColor: theme.palette.primary.main,
          marginLeft: '1em',
          '&:hover': {
            bgcolor: alpha(theme.palette.primary.main, 0.8),
          },
        },
      }}
    >
      <div>
        <Button
          variant="contained"
          className="btn-smooth btn-clear"
          startIcon={<CrossIcon />}
          onClick={onClearStripsClick}
        >
          整筆清除
        </Button>

      </div>
      <div>
        {/* <Button
          onClick={onRepayClick}
          variant="contained"
          className="btn-smooth btn-return"
          startIcon={<PlayForWork />}
        >
          還款
        </Button> */}
        <Button
          onClick={onReturnClick}
          variant="contained"
          className="btn-smooth btn-return"
          startIcon={<SwapVerticalCircle />}
        >
          退貨
        </Button>
        {/* <Button
          onClick={onReceiptClick}
          variant="contained"
          className="btn-smooth btn-return"
          startIcon={<SwapVerticalCircle />}
        >
          收據
        </Button> */}
        <Toggle determineParam={repayOpen}>
          <DialogRepay
            open={repayOpen}
            handleClose={onRepayClose}
          />
        </Toggle>
        <Toggle determineParam={returnOpen}>
          <DialogReturn
            open={returnOpen}
            handleClose={onReturnClose}
          />
        </Toggle>
        <Button
          variant="contained"
          className="btn-smooth btn-money"
          onClick={onMoneyClick}
          startIcon={<PointOfSale />}>
          錢櫃
        </Button>
        <Button
          onClick={onCheckoutClick}
          variant="contained"
          className="btn-smooth btn-settlement"
          startIcon={<CalcIcon />}>
          小計
        </Button>
        <DialogCheckout
          open={checkoutOpen}
          isPartialClear={isPartialClear}
          handleCheckoutClose={handleCheckoutClose}
          handleValidateAndClose={handleValidateAndClose}
          setIsPartialClear={setIsPartialClear}
        />
        {/* <Toggle determineParam={checkoutOpen}>
            <DialogCheckout
              open={checkoutOpen}
              handleClose={() => setCheckoutOpen(false)}
            />
          </Toggle> */}

      </div>
    </Box>
  )
}

const CrossIcon = () => {
  return (
    <SvgIcon>
      <path d="M11.381 0C5.09391 0 0 5.41092 0 12.0893C0 18.7676 5.09391 24.1785 11.381 24.1785C17.6681 24.1785 22.762 18.7676 22.762 12.0893C22.762 5.41092 17.6681 0 11.381 0ZM16.9613 15.2627C17.177 15.4918 17.177 15.8623 16.9613 16.0914L15.144 18.0169C14.9284 18.246 14.5796 18.246 14.3639 18.0169L11.381 14.8191L8.39347 18.0169C8.17779 18.246 7.82901 18.246 7.61333 18.0169L5.80063 16.0865C5.58494 15.8574 5.58494 15.4869 5.80063 15.2578L8.81108 12.0893L5.80063 8.91583C5.58494 8.68672 5.58494 8.31624 5.80063 8.08713L7.61792 6.15675C7.8336 5.92764 8.18237 5.92764 8.39806 6.15675L11.381 9.35943L14.3685 6.16162C14.5842 5.93251 14.9329 5.93251 15.1486 6.16162L16.9659 8.09201C17.1816 8.32112 17.1816 8.6916 16.9659 8.92071L13.9509 12.0893L16.9613 15.2627Z" fill="#FAFAFA" />
    </SvgIcon>
  )
}

