import React, { useState, useEffect, useMemo } from 'react'
import { connect } from 'react-redux'

import Box from '@mui/material/Box'
import Dialog from '@mui/material/Dialog'
import Button from '@mui/material/Button'
import IconButton from '@mui/material/IconButton'
import Typography from '@mui/material/Typography'
import DialogContent from '@mui/material/DialogContent'
import DialogActions from '@mui/material/DialogActions'
import OrderList from '../../ui/OrderList'

import Cancel from '@mui/icons-material/Cancel'
import SwapVerticalCircle from '@mui/icons-material/SwapVerticalCircle'

import { salesRequest } from '../../../apis'
import { fireDialogToast } from '../../../helpers/sweetAlert'
import { replacePosStrips } from '../../../slices/posStripsSlice'
import { replaceCurrentMember } from '../../../slices/currentMemberSlice'
import { fetchSales, deleteSale } from '../../../slices/salesSlice'

const DialogReturn = ({
  auth,
  open,
  sales,
  deleteSale,
  fetchSales,
  handleClose,
}) => {
  const [currentOrderId, setCurrentOrderId] = useState('')

  const currentPaidOrder = useMemo(() => {
    return (sales.list && sales.list.find(order => order.id === currentOrderId)) || {}
  }, [sales, currentOrderId])

  const currentCreditInfo = useMemo(() => {
    return (currentPaidOrder?.salesPayments
      && currentPaidOrder.salesPayments.find(payment => Boolean(payment.creditCard))?.creditCard)
      || {}
  }, [currentPaidOrder])

  useEffect(() => {
    fetchSales({
      isReturn: 0,
    })
  }, [fetchSales])

  useEffect(() => {
    if (sales.list && sales.list[0]) setCurrentOrderId(sales.list[0].id);
  }, [sales])

  const onReturnClick = async () => {
    try {
      await salesRequest.post(`/refund/${currentOrderId}`, null, {
        headers: { 'Authorization': 'Bearer ' + auth.authenticated },
      })
      deleteSale(currentOrderId)
      fireDialogToast('退貨完成', 'success')

    } catch (error) {
      fireDialogToast(`警告: ${error}`, 'error')
    }
  }

  const renderPayments = payments => {
    return payments.map((payment) => {
      const paymentType = payment.SalePointPayment || {}

      return (
        <div key={payment.id}>
          <span className="received-method">{paymentType.paymentName}：</span>
          <span className="received-content">{payment.value}</span>
        </div>
      )
    })
  }

  return (
    <Dialog
      id="dialog-temp-order"
      open={open}
      onClose={handleClose}
      aria-labelledby="dialog-temp-order"
      maxWidth="md"
      fullWidth
    >
      <Box
        sx={{
          flex: '0 0 auto',
          margin: 0,
          padding: '0.375em 1.25em 0.25em',
          lineHeight: 1.6,
          letterSpacing: '0.0075rem',
          bgcolor: 'background.default',
          '& h6': {
            color: 'secondary.main',
            fontSize: '1.375rem',
            fontWeight: 'bold',
            letterSpacing: '0.05em',
          }
        }}
      >
        <Typography variant="h6">訂單退貨</Typography>
        {handleClose ? (
          <IconButton aria-label="close" onClick={handleClose} sx={{
            position: 'absolute',
            right: '0.5em',
            top: '0.25em',
            color: '#cb5c5c',
            padding: 0,
          }}>
            <Cancel sx={{ width: '1.25em', height: '1.25em' }} />
          </IconButton>
        ) : null}
      </Box>

      <DialogContent
        sx={{
          padding: '1.25em 1.75em 0',
          bgcolor: 'background.paper',
        }}
      >
        <OrderList
          sales={sales}
          fetchSales={fetchSales}
          currentOrderId={currentOrderId}
          setCurrentOrderId={setCurrentOrderId}
          currentPaidOrder={currentPaidOrder}
        />

      </DialogContent>

      <DialogActions sx={dialogActionStyle}>
        <div className="order-info">
          <div className="received-field">
            <div>
              <Typography variant="subtitle2" className="received-title">
                當筆付款方式 :
              </Typography>
            </div>
            <div className="received-pool">

              {renderPayments((currentPaidOrder && currentPaidOrder.salesPayments) || [])}

            </div>
          </div>

          <div className="other-field">
            <div>
              <Typography variant="subtitle2" className="other-title">
                其他詳細資訊
              </Typography>
            </div>
            <div className="other-pool">
              <div>
                <span className="other-method">卡號末四碼：</span>
                <span className="other-content">
                  {currentCreditInfo.cardNo ? currentCreditInfo.cardNo.replaceAll(' ', '').slice(-4) : '- - - -'}
                </span>
              </div>
              <div>
                <span className="other-method">授權碼：</span>
                <span className="other-content">{currentCreditInfo.approvalCode || '- - - -'}</span>
              </div>
              <div>
                <span className="other-method">載具號碼：</span>
                <span className="other-content">{currentPaidOrder.phoneBarcode || '- - - -'}</span>
              </div>
              <div>
                <span className="other-method">統一編號：</span>
                <span className="other-content">{currentPaidOrder.uniformNumber || '- - - -'}</span>
              </div>
            </div>
          </div>

        </div>
        <div className="returnBtn-wrapper">
          <Button
            startIcon={<SwapVerticalCircle sx={{ '&:first-of-type': { fontSize: '1.25em' } }} />}
            onClick={onReturnClick}
            sx={{
              display: 'inline-flex',
              justifyContent: 'center',
              fontSize: '1.375rem',
              fontWeight: 'bold',
              borderRadius: '0.5em',
              border: 'none',
              color: 'background.paper',
              bgcolor: (theme) => theme.palette.primary.main,
              minWidth: '105px',
              '&:hover': {
                bgcolor: '#fabd5e',
              },
            }}
          >
            退貨
          </Button>
        </div>
      </DialogActions>

    </Dialog>
  )
}

const dialogActionStyle = {
  justifyContent: 'space-between',
  padding: '1em 1.75em',
  bgcolor: 'background.paper',
  '& .order-info': {
    flexGrow: 1,
    display: 'flex',
    flexWrap: 'wrap',
  },
  '& .received-field': {
    flex: '0 0 36%',
    display: 'flex',
    flexWrap: 'wrap',
    padding: '0 0.25em',
    '& > div': {
      flex: '0 0 100%',
    },
  },
  '& .received-title': {
    display: 'inline-block',
    borderRadius: '0.25em',
    bgcolor: 'active.main',
    padding: '0 0.25em',
  },
  '& .received-pool': {
    display: 'inline-flex',
    flexWrap: 'wrap',
    '& > div': {
      flex: '1 0 50%',
      display: 'flex',
      fontSize: '0.875rem',
    },
  },
  '& .received-method': {
    minWidth: 0,
  },
  '& .received-content': {
    flexGrow: 1,
    minWidth: 0,
  },
  '& .other-field': {
    flexGrow: 1,
    flexShrink: 1,
    padding: '0 0.25em',
    '& > div': {
      flex: '0 0 100%',
    },
  },
  '& .other-title': {
    display: 'inline-block',
    borderRadius: '0.25em',
    bgcolor: 'active.main',
    padding: '0 0.25em',
  },
  '& .other-pool': {
    display: 'flex',
    flexWrap: 'wrap',
    '& > div': {
      flex: '0 0 50%',
      display: 'flex',
      fontSize: '0.875rem',
    },
  },
  '& .other-content': {
    flexGrow: 1,
  },
  '& .returnBtn-wrapper': {
    display: 'flex',
    alignItems: 'center',
  },
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    sales: state.sales,
  }
}

export default connect(
  mapStateToProps,
  {
    fetchSales,
    deleteSale,
    replacePosStrips,
    replaceCurrentMember,
  }
)(DialogReturn)