import React, { useRef, useState, useEffect, useCallback } from 'react'
import { connect } from 'react-redux'
import { getNewStrip } from '../../helpers/checkout'
import { addPosStrip } from '../../slices/posStripsSlice'
import { fetchComms, replaceComms } from '../../slices/commsSlice'
import { Button, Typography, Box } from '@mui/material'
import { useTheme, alpha } from '@mui/system'

const SearchComm = ({
  comms, fetchComms, replaceComms, addPosStrip, setActiveCommId, setUniformContent, listRef
}) => {
  const theme = useTheme()
  const [searchContent, setSearchContent] = useState('')
  const [activeSearchIndex, setActiveSearchIndex] = useState(0)
  const containerRef = useRef(null)
  const activeItemRef = useRef(null)
  const commListRef = useRef(null)

  const onSearchClick = e => {
    e.preventDefault()
    fetchComms(searchContent ? { name: searchContent } : {})
    document.body.addEventListener('click', onBodyClick, { capture: true })
  }

  const onCommClick = async (comm) => {
    if (!comm.stockId) return
    const newStrip = getNewStrip(comm)
    await addPosStrip(newStrip)
    setUniformContent('')
    setActiveCommId(newStrip.id)
    scrollToBottom(listRef.current)
  }

  const onCommKeyUp = (e, comm) => {
    if (e.code === 'Enter') {
      onCommClick(comm)
    }
  }

  const scrollToBottom = el => {
    el.scrollTo(0, el.scrollHeight)
  }

  const onBodyClick = useCallback((e) => {
    if (containerRef.current && containerRef.current.contains(e.target)) return
    replaceComms([])
    setActiveSearchIndex(0)
    document.body.removeEventListener('click', onBodyClick, { capture: true })
  }, [replaceComms])

  useEffect(() => {
    if (containerRef.current && Object.keys(comms).length > 0) {
      document.body.addEventListener('click', onBodyClick, { capture: true })
    }
    return () => {
      document.body.removeEventListener('click', onBodyClick, { capture: true })
    }
  }, [onBodyClick, comms])

  const renderSearchResult = () => {
    return comms.map((searchComm, index) => {
      const active = index === activeSearchIndex ? 'active' : ''
      const ref = index === activeSearchIndex ? activeItemRef : null
      const combinedName = searchComm.description + ' - ' + searchComm.stockId
      return (
        <div
          onClick={() => onCommClick(searchComm)}
          onKeyUp={e => onCommKeyUp(e, searchComm)}
          className={`search-item ${active}`}
          data-id={searchComm.stockId}
          tabIndex="0"
          key={index}
          ref={ref}
        >
          <div className="comm-name micro-scroll">
            <Typography noWrap={true} variant="body1" title={combinedName}>
              {combinedName}
            </Typography>
          </div>
          <small className="comm-price">{searchComm.price ? searchComm.price.price : 0}</small>
        </div>
      )
    })
    // return <div className="search-list" ref={commListRef}>{searchComms}</div>;
  }

  return (
    <Box component="section" id="search-comm" sx={styles.root}>
      <div className="search-comm-container" ref={containerRef}>
        <Box component="form" className="search-bar" sx={{
          display: 'flex',
          width: '100%',
          '& .comm-input': {
            width: 'calc(100% - 4.5rem)',
            border: `1px solid ${theme.palette.primary.main}`,
            paddingLeft: '0.5em',
            outlineColor: theme.palette.primary.main,
            '&::placeholder': {
              color: '#c4c4c4',
            },
          },
          '& .submit-btn': {
            width: '4.5rem',
            fontSize: '1rem',
            borderRadius: 0,
            padding: '0.25em 1em',
            color: 'background.paper',
            backgroundColor: theme.palette.primary.main,
            '&:hover': {
              bgcolor: alpha(theme.palette.primary.main, 0.8),
            },
          },
        }}>
          <input
            type="text"
            className="comm-input"
            value={searchContent}
            onChange={e => setSearchContent(e.target.value)}
            placeholder="在此輸入商品名稱"
          />
          <Button
            onClick={onSearchClick}
            className="submit-btn"
            variant="contained"
            disableElevation
            type="submit"
          >
            搜尋
          </Button>
        </Box>
        <Box className="search-list" ref={commListRef} sx={styles.searchList}>
          {renderSearchResult()}
        </Box>
      </div>
    </Box>
  )
}

const styles = {
  root: {
    flex: '0 0 50%',
    display: 'flex',
    alignItems: 'flex-end',
    justifyContent: 'center',
    flexDirection: 'column',
    minWidth: 0,
    '& .search-comm-container': {
      position: 'relative',
      flex: '0 0 85%',
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'flex-start',
      alignContent: 'flex-start',
      width: '100%',
      paddingRight: '1em',
      minHeight: 0,
    },
  },
  searchList: {
    flexGrow: 1,
    width: '100%',
    display: 'flex',
    flexDirection: 'column',
    overflow: 'auto',
    minHeight: 0,
    '& .search-item': {
      flex: '0 0 20%',
      display: 'inline-flex',
      alignItems: 'center',
      justifyContent: 'space-between',
      cursor: 'pointer',
      backgroundColor: 'background.light',
      borderBottom: '1px solid #dddddd',
      padding: '0 0.5em',
      minHeight: 0,
      '&:focus': {
        zIndex: 2,
        bgcolor: 'active.main',
        outline: 'none',
      },
      '&:last-of-type': {
        borderBottom: 'none',
      },
    },
    '& .comm-name': {
      display: 'flex',
      fontWeight: 'bold',
      letterSpacing: '0.04em',
      paddingRight: '0.5em',
      color: '#585a63',
      overflow: 'overlay',
      '& p': {
        display: 'inline-block',
      },
      '& small': {
        fontSize: '0.75em',
        color: '#585a63',
      },
    },
    '& .comm-price': {
      letterSpacing: '0.02em',
    },
  },
}

const mapStateToProps = state => {
  return {
    comms: state.comms
  }
}

export default connect(
  mapStateToProps,
  { addPosStrip, fetchComms, replaceComms }
)(SearchComm)