import React, { useState, useEffect, useMemo, useRef } from 'react'
import { omit } from 'lodash-es'
import { alpha } from '@mui/material'
import { connect } from 'react-redux'
import { CheckCircle, Check } from '@mui/icons-material'
import { Box, Grid, Button, DialogContent } from '@mui/material'
import PrintedOrder from '../../ui/PrintedOrder'
import useBarcode from '../../../hooks/useBarcode'
import { fireDialogToast } from '../../../helpers/sweetAlert'
import { printReceipt } from '../../../helpers/checkout'
import { checkout } from '../../../slices/posStripsSumSlice'
import { replaceCreditCardResponse } from '../../../slices/creditCardResponseSlice'
import { updateReceivedAmounts, replaceReceivedAmounts } from '../../../slices/receivedAmountsSlice'
// import { RECEIVED_METHOD_MAPPING, PAYMENT_METHOD_MAPPING } from '../../../publicVariable'

const taxIdRegex = new RegExp('^\\d{8}$')
const barCodeRegex = new RegExp('^\\/{1}[0-9A-Z.+-]{7}$')
const loveCodeRegex = new RegExp('^\\d{3,7}$')
const certificateRegex = new RegExp('^[A-Z]{2}\\d{14}$')


function DialogCheckoutContent({
  handleCheckoutClose,
  isPartialClear,
  setIsPartialClear,
  user,
  posStrips,
  posStripsSum,
  currentMember,
  paymentMethods,
  receivedAmounts,
  creditCardResponse,
  replaceCreditCardResponse,
  updateReceivedAmounts,
  replaceReceivedAmounts,
  checkout,
}) {
  const printContentRef = useRef(null)
  const [inputAmount, setInputAmount] = useState(0)
  const [unreceiveAmount, setUnreceiveAmount] = useState(0)
  const [giveChangeAmount, setGiveChangeAmount] = useState(0)
  const [isTaxIdChecked, setIsTaxIdChecked] = useState(false)
  const [taxId, setTaxId] = useState('')
  const [carrierType, setCarrierType] = useState('')
  const [invoiceCarrier, setInvoiceCarrier] = useState('')
  const [printOrder, setPrintOrder] = useState(null)
  const receiptMode = useMemo(() => (
    window.localStorage.getItem(`receiptMode/${user.salePointId}`)
  ), [user])

  const [barcode, clearBarcode] = useBarcode()

  useEffect(() => {
    if (!barcode) return
    setInvoiceCarrier(barcode)
    distinguishCarrierType(barcode)
    clearBarcode()
  }, [barcode, clearBarcode])

  const onCheckoutClick = async () => {
    if (!validateCheckout()) return
    const successData = await checkout({
      giveChangeAmount,
      ...(isTaxIdChecked && { taxId }),
      ...(carrierType && { invoiceCarrier }),
    })

    if (successData) {
      if (receiptMode === '印表機') {
        setPrintOrder(successData)
      } else {
        printReceipt(successData)
      }
      handleCheckoutClose()
    }
  }

  useEffect(() => {
    if (printOrder) {
      const printContent = printContentRef.current
      const printIframe = document.querySelector('#print-iframe')
      printIframe.innerHTML = printContent.innerHTML
      window.print()

      printIframe.innerHTML = null
      setPrintOrder(null)
    }

  }, [printOrder])

  // 驗證可否結帳
  const validateCheckout = () => {
    if (unreceiveAmount > 0) {
      fireDialogToast('提醒: 款項尚未收齊')
      return false
    }
    if (taxId && !isTaxIdChecked) {
      fireDialogToast('提醒: 統一編號不符合')
      return false
    }
    if (invoiceCarrier && !carrierType) {
      fireDialogToast('提醒: 載具號碼不符合')
      return false
    }
    return true
  }

  // 計算已收金額
  const receivedSum = useMemo(() => {
    return Object.values(receivedAmounts).reduce((previousValue, currentValue) => (
      previousValue + currentValue
    ), 0)
  }, [receivedAmounts])

  // 計算找零金額與未收金額
  useEffect(() => {
    const newUnreceiveAmount = posStripsSum.total - receivedSum
    if (newUnreceiveAmount > 0) {
      setGiveChangeAmount(0)
      setUnreceiveAmount(newUnreceiveAmount)
    } else {
      setGiveChangeAmount(-newUnreceiveAmount)
      setUnreceiveAmount(0)
    }
  }, [posStripsSum, receivedSum])

  const onInputAmountChange = e => {
    if (Number.isNaN(Number(e.target.value))) return
    setInputAmount(Number(e.target.value))
  }

  // 將未收金額填入[輸入欄位]
  const onFillInClick = () => {
    if (unreceiveAmount > 0) {
      setInputAmount(unreceiveAmount)
    }
  }

  // 依照模式判斷付款按鈕之功能
  const onPaymentClick = e => {
    if (isPartialClear) return removeReceived(e)
    if (!inputAmount && (unreceiveAmount === 0)) return fireDialogToast('提醒: 已可結帳')
    if (!inputAmount) return fireDialogToast('提醒: 請輸入金額')

    if (e.target.dataset.name === 'cash') {
      onCashClick(e)
    } else if (e.target.dataset.name === 'credit') {
      onCreditClick(e)
    } else {
      addReceived(e)
    }
  }

  const onCashClick = e => {
    // 在這裡打開**收銀機**

    // 目前假定現金的 salePointPaymentId 固定為'1'
    // 所以將 salePointPaymentId 寫定為'1'在按鈕dataset上,
    addReceived(e)
  }

  const onCreditClick = async e => {
    // 在這裡處理**信用卡機**
    if (Object.keys(creditCardResponse).length > 0) {
      fireDialogToast('提醒: 已有信用卡付款資料')
      return
    }

    const response = await window.edcBridge.send({
      amount: inputAmount,
      edc: window.configBridge.getConfig()?.edc,
    })

    if (!response.success) {
      fireDialogToast('警告: 付款失敗', 'error')
      return
    }

    replaceCreditCardResponse(response)
    fireDialogToast('信用卡付款成功', 'success')
    // 以下暫用其他付款方式的紀錄已收金額
    // 並暫時將 salePointPaymentId 寫定為'2'在按鈕dataset上,
    // 之後改為根據卡機變更 salePointPaymentId
    addReceived(e)
  }

  // 以[選定付款方式]付[輸入欄位]之金額
  const addReceived = e => {
    const receivedPaymentId = e.target.dataset.paymentId
    // 僅允許現金有找零
    if ((receivedPaymentId !== '1') && (inputAmount > unreceiveAmount)) return alert('輸入金額不能超過未收金額')

    const initialAmount = receivedAmounts[receivedPaymentId] || 0
    updateReceivedAmounts({ [receivedPaymentId]: initialAmount + inputAmount })
    setInputAmount(0)
  }

  // 清除[選定付款方式]之已付金額
  const removeReceived = e => {
    const receivedPaymentId = e.target.dataset.paymentId
    const targetReceiveAmount = receivedAmounts[receivedPaymentId]
    if (!targetReceiveAmount) return
    if (!inputAmount || (targetReceiveAmount === inputAmount)) {
      replaceReceivedAmounts(omit(receivedAmounts, [receivedPaymentId]))
    }
    else if (targetReceiveAmount > inputAmount) {
      updateReceivedAmounts({ [receivedPaymentId]: targetReceiveAmount - inputAmount })
      setInputAmount(0)
    }
  }

  // 發票相關輸入
  const onTaxIdChange = e => {
    setTaxId(e.target.value)
    if (taxIdRegex.test(e.target.value)) {
      setIsTaxIdChecked(true)
    } else {
      setIsTaxIdChecked(false)
    }
  }

  const distinguishCarrierType = newValue => {
    if (barCodeRegex.test(newValue)) {
      setCarrierType('barCode')
    } else if (loveCodeRegex.test(newValue)) {
      setCarrierType('loveCode')
    } else if (certificateRegex.test(newValue)) {
      setCarrierType('certificate')
    } else {
      setCarrierType('')
    }
  }

  const onInvoiceCarrierChange = e => {
    const newValue = e.target.value
    setInvoiceCarrier(newValue)
    distinguishCarrierType(newValue)
  }

  // 清除所有已付金額
  const onAllReceivedClearClick = () => {
    replaceReceivedAmounts({})
    replaceCreditCardResponse({})
  }

  // 付款按鈕切換成[部分清除模式]
  const onPartialClearClick = () => {
    setIsPartialClear(true)
  }

  // 解除[部分清除模式]模式
  const onPartialClearCancelClick = () => {
    setIsPartialClear(false)
  }

  // UI判斷[部分清除模式]之參數
  const partialClearing = isPartialClear ? 'clearing' : ''

  const renderReceivedList = receivedAmounts => {
    const paymentIds = Object.keys(receivedAmounts)
    return paymentIds.map(paymentId => {
      return (
        <div key={paymentId}>
          <span>{paymentMethods[paymentId].paymentName} :</span>
          <span>{receivedAmounts[paymentId]}</span>
        </div>
      )
    })
  }

  const renderMinorPaymentBtn = paymentMethods => {
    const minorPaymentButtons = []

    Object.keys(paymentMethods).forEach(paymentId => {
      const typeId = paymentMethods[paymentId].salePointPaymentsTypeId
      if (typeId === '1' || typeId === '2') return
      minorPaymentButtons.push(
        <Button
          key={paymentId}
          color="secondary"
          variant="outlined"
          onClick={onPaymentClick}
          data-payment-id={paymentId}
        >
          {paymentMethods[paymentId].paymentName}
        </Button>
      )
    })

    return minorPaymentButtons
  }

  const renderCheckoutDetail = list => {
    return list.map((item, index) => {
      return (
        <div className="table-item" key={index}>
          <div className="serial-sec sec-item">{index + 1}</div>
          <div className="comm-name-sec sec-item">{item.stockName}</div>
          <div className="price-sec sec-item">{item.price}</div>
          <div className="quantity-sec sec-item">{item.quantity}</div>
          <div className="discount-sec sec-item">{item.discount}</div>
          <div className="total-sec sec-item">{item.total}</div>
        </div>
      )
    })
  }


  return (
    <DialogContent sx={{
      padding: '0.5em 1.75em 0',
      backgroundColor: 'background.paper',
    }}>

      <Box
        className="member-info"
        sx={{
          '& > div': {
            display: 'inline-block',
            fontSize: '0.9rem',
            color: 'secondary.matte',
            paddingRight: '1em',
          },
          '& .content': {
            fontWeight: 'bold',
          },
        }}
      >
        {
          currentMember.memberName && (
            <div>
              <span className="label">會員名稱 : </span>
              <span className="content">{currentMember.memberName || ''}</span>
            </div>
          )
        }

        {/* <div>
          <span className="label">點數 : </span>
          <span className="content">100000點</span>
        </div>
        <div>
          <span className="label">儲值金 : </span>
          <span className="content">100000元</span>
        </div> */}
      </Box>

      <Grid container>
        <Grid item xs={5} sx={{
          display: 'flex',
          flexDirection: 'column',
        }}>
          <Box
            className={`received-amount-input ${partialClearing}`}
            sx={styles.receivedAmountInput}
          >
            <input
              value={inputAmount || ''}
              onChange={onInputAmountChange}
              placeholder="輸入收款金額" />
            <Button
              variant="contained"
              disableElevation
              disabled={isPartialClear || unreceiveAmount <= 0}
              onClick={onFillInClick}
            >
              填入
            </Button>
          </Box>

          <Box
            className="received-amount-list"
            sx={styles.receivedAmountList}
          >
            {renderReceivedList(receivedAmounts)}
          </Box>

          {creditCardResponse.success && (
            <Box
              className="received-amount-list credit-info"
              sx={styles.receivedAmountList}
            >
              <div>
                <span>授權碼 :</span>
                <span>{creditCardResponse.response?.approvalNo?.replaceAll(' ', '')}</span>
              </div>
              <div>
                <span>末四碼 :</span>
                <span>{creditCardResponse.response?.cardNo?.slice(-4)}</span>
              </div>
            </Box>
          )}

        </Grid>
        <Grid item xs={7} sx={{ paddingLeft: '2em' }}>
          <Box
            className={`main-payment-method ${partialClearing}`}
            sx={styles.mainPaymentMethod}>
            <Button data-payment-id="1" data-name="cash" onClick={onPaymentClick}>現金</Button>
            <Button data-payment-id="2" data-name="credit" onClick={onPaymentClick}>信用卡</Button>
          </Box>
          <Box
            className={`minor-payment-method ${partialClearing}`}
            sx={styles.minorPaymentMethod}
          >
            {renderMinorPaymentBtn(paymentMethods)}
            {/* <Button variant='outlined' color="secondary" data-name="remittance" onClick={onPaymentClick}>匯款</Button>
            <Button variant='outlined' color="secondary" data-name="discount" onClick={onPaymentClick}>整筆折扣</Button>
            <Button variant='outlined' color="secondary" data-name="point" onClick={onPaymentClick}>點數折扣</Button>
            <Button variant='outlined' color="secondary" data-name="stored" onClick={onPaymentClick}>儲值金</Button> */}
          </Box>
        </Grid>
      </Grid>

      <Grid container>
        <Grid item container
          className={`payment-status ${partialClearing}`}
          sx={styles.paymentStatus}
        >
          <Grid item>
            未收金額 : <span className="unreceived">
              {unreceiveAmount}
            </span>
          </Grid>
          <Grid item>
            找零金額 : <span>{giveChangeAmount}</span>
          </Grid>
          <Grid item>
            已收金額 : <span>{receivedSum}</span>
          </Grid>
          <Grid item>
            應收金額 : <span>{posStripsSum.total}</span>
          </Grid>
        </Grid>
        <Grid
          item
          className="checkout-btn-wrapper"
          sx={styles.checkoutBtnWrapper}
        >
          <Button
            variant="contained"
            disableElevation
            disabled={unreceiveAmount > 0}
            onClick={onCheckoutClick}
            startIcon={<CheckCircle sx={{ '&:first-of-type': { fontSize: '1.25em' } }} />}
          >
            結帳
          </Button>
        </Grid>

      </Grid>

      <Grid
        container
        className="invoice-info"
        sx={styles.invoiceInfo}
      >
        <div className="input-wrapper">
          <span>統一編號</span>
          <input
            type="text"
            value={taxId}
            onChange={onTaxIdChange}
          />
          {isTaxIdChecked && <Check />}
        </div>

        <div className="input-wrapper">
          <span>載具號碼</span>
          <input
            type="text"
            value={invoiceCarrier}
            onChange={onInvoiceCarrierChange}
          />
          {carrierType && <Check />}
        </div>

        <div className="input-wrapper">
          <span>載具類型</span>
          <select value={carrierType} disabled >
            <option value="">- 將依載具號碼判斷 -</option>
            <option value="barCode">手機條碼</option>
            <option value="loveCode">愛心碼</option>
            <option value="certificate">自然人憑證</option>
          </select>
        </div>
      </Grid>

      <Box sx={{ padding: '1.5em 0' }}>
        <Box
          className="order-detail"
          sx={styles.orderDetail}
        >
          <div className="table-header">
            <div className="serial-sec sec-item">項次</div>
            <div className="comm-name-sec sec-item">商品名稱</div>
            <div className="price-sec sec-item">定價</div>
            <div className="quantity-sec sec-item">數量</div>
            <div className="discount-sec sec-item">折數</div>
            <div className="total-sec sec-item">合計</div>
          </div>

          <div className='table-body'>
            {renderCheckoutDetail(posStrips)}
          </div>
        </Box>
      </Box>

      <Box
        className="clear-method"
        sx={styles.clearMethod}
      >
        <Button className="all-clear" variant='outlined' color='error' onClick={onAllReceivedClearClick}>
          清除付款
        </Button>
        {isPartialClear ? (
          <Button className="partial-clear clearing" variant='outlined' color="primary"
            onClick={onPartialClearCancelClick}
          >
            結束部分清除
          </Button>
        ) : (
          <Button className="partial-clear" variant='outlined' color="secondary" onClick={onPartialClearClick}>
            部分清除付款
          </Button>
        )}
      </Box>

      <Box className="print-content" ref={printContentRef}
        sx={{
          display: 'none',
        }}
      >
        {printOrder && <PrintedOrder order={printOrder} />}
      </Box>
    </DialogContent>
  )
}

