import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import { Box } from '@mui/material'
import { initialVisits, renewVisits } from '../../slices/visitsSlice'

const InfoHeader = props => {
  const [dateString, setDateString] = useState('')
  const [timeString, setTimeString] = useState('')
  const { initialVisits, renewVisits, visits } = props

  useEffect(() => {
    setFormatDate()
    const clockInterval = setInterval(() => {
      renewVisits()
      setFormatDate()
      console.log('30s Passed.')
    }, 30000)
    return () => {
      clearInterval(clockInterval)
    }
  }, [renewVisits])

  useEffect(() => {
    initialVisits()
  }, [initialVisits])

  const setFormatDate = () => {
    const newDate = new Date()
    const dateString = newDate.toLocaleDateString()
    const dayString = '星期' + newDate.getDay().toLocaleString('zh-Hant-TW-u-nu-hanidec')

    const originTimeString = newDate.toLocaleTimeString()
    const slicedTimeString = originTimeString.slice(0, -3)
    const timeString = slicedTimeString.replace('午', '午 ')

    setDateString(dateString + ' ' + dayString);
    setTimeString(timeString)
  }

  const memberActive = Object.keys(props.currentMember).length > 0 ? 'active' : ''

  return (
    <Box component="section" id="info-header" sx={{
      flex: '0 1 5 %',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'space-between',
      backgroundColor: 'background.darkPaper',
      boxShadow: 'inset -10px 0px 15px -10px #aaaaaa',
      padding: '0 2%',
      '& .consumer-serial': {
        display: 'inline-block',
        fontWeight: 'bold',
        paddingRight: '2em',
      },
      '& .member-name': {
        display: 'inline-block',
        fontWeight: 'bold',
        color: '#c4c4c4',
      },
      '& .member-name.active span': {
        color: '#91bbcb',
        textShadow: theme => `1px 0px 1px ${theme.palette.background.default}`,
      },
      '& .time-field': {
        fontSize: '0.875rem',
        display: 'inline-block',
        fontWeight: 'bold',
        textAlign: 'right',
        color: 'secondary.main',
      },
    }}>
      <div>
        <div className="consumer-serial">
          <span>客次&nbsp;:&nbsp;{visits.visitsNum}</span>
        </div>
        <div className={`member-name ${memberActive}`}>
          <span>
            會員&nbsp;:&nbsp;
            {props.currentMember.memberName}
          </span>
        </div>
      </div>

      <div className="time-field">
        <div>{dateString}</div>
        <div>{timeString}</div>
      </div>
    </Box>
  )
}

const mapStateToProps = state => {
  return {
    currentMember: state.currentMember,
    visits: state.visits,
  }
}

export default connect(
  mapStateToProps,
  { initialVisits, renewVisits }
)(InfoHeader)
