import React from 'react'
import { Grid, Typography, Card, Checkbox } from '@mui/material'

function SalePointCard({
  salePoint,
  selectedSalePoints,
  handleSelectSalePoint,
}) {
  return (
    <Card onClick={() => handleSelectSalePoint(salePoint)}>
      <Grid container wrap="nowrap" spacing={2}>
        <Grid item container xs="auto" alignItems="center">
          <Checkbox
            readOnly
            checked={selectedSalePoints.some(el => el.id === salePoint.id)}
          />
        </Grid>
        <Grid item container xs alignItems="center" zeroMinWidth>
          <Typography className='salePoint-name' variant="subtitle1" noWrap title={salePoint.salePointName}>
            {salePoint.salePointName}
          </Typography>

          {/* <Typography className='salePoint-id' variant="body2" noWrap>
            {salePoint.stockId}
          </Typography> */}
        </Grid>
      </Grid>
    </Card>
  )
}

export default SalePointCard