import React from 'react'
import Box from '@mui/material/Box'
import Grid from '@mui/material/Grid'
import Paper from '@mui/material/Paper'

export const showOrderTime = timestamp => {
  return new Date(timestamp).toLocaleString()
}

export const renderPayment = payments => {
  console.log(payments)
  return payments.map(payment => {
    return (
      <div className="received-sec" key={payment.id}>
        <span>
          {`${payment.SalePointPayment?.paymentName} : `}
        </span>
        <span className="main-value">
          {payment.value}
        </span>
      </div>
    )
  })
}

export const OrderItemDetail = ({ list }) => {
  return (
    <Box className="order-table">
      <div className="table-header">
        <div className="serial-sec">項次</div>
        <div className="name-sec">商品名稱</div>
        <div className="clerk-sec">服務員</div>
        <div className="price-sec">定價</div>
        <div className="quantity-sec">數量</div>
        <div className="discount-sec">折數</div>
        <div className="total-sec">合計</div>
      </div>

      <div className="table-list">
        {list.map((item, index) => {
          return (
            <div className="table-item" key={item.id}>
              <div className="serial-sec">{index + 1}</div>
              <div className="name-sec">
                {(item.stockMaster && item.stockMaster.description) || item.stockDescription}
              </div>
              <div className="clerk-sec">{item.user}</div>
              <div className="price-sec">{item.price}</div>
              <div className="quantity-sec">{item.quantity}</div>
              <div className="discount-sec">
                {((item.price * item.quantity) - item.subTotal) || ''}
              </div>
              <div className="total-sec">{item.subTotal}</div>
            </div>
          )
        }
        )}
      </div>
    </Box>
  )
}

export const PrintedOrder = ({ order }) => {
  const creditCardInfo = order?.salesPayments?.find(payment => Boolean(payment.creditCard))?.creditCard || {}

  return (
    <Paper
      sx={{
        width: '100%',
        minHeight: '100%',
        overflow: 'hidden',
        '& .order-table': orderTableStyle,
      }}
    >
      <Grid container alignItems="center"
        sx={{
          px: '0.5rem',
          py: '0.25rem',
          bgcolor: theme => theme.palette.background.default,
        }}
      >

        <Grid item xs>
          <Grid container>
            <Grid item xs container>
              <Grid item xs="auto">
                日期 :&nbsp;
              </Grid>
              <Grid item xs className="bold">
                {showOrderTime(order.date)}
              </Grid>
            </Grid>

            <Grid item xs container>
              <Grid item xs="auto">
                會員 :&nbsp;
              </Grid>
              <Grid item xs className="bold">
                {order.memberInfo ? `${order.memberInfo.memberName} | ${order.memberInfo.phone}` : '---'}
              </Grid>
            </Grid>

            <Grid item xs container>
              <Grid item xs="auto">
                銷售員 :&nbsp;
              </Grid>
              <Grid item xs className="bold">
                {order.salesman && order.salesman.realName}
              </Grid>
            </Grid>
          </Grid>

          <Grid container>
            <Grid item xs container>
              <Grid item xs="auto">
                門市 :&nbsp;
              </Grid>
              <Grid item xs className="bold">
                {(order?.cashRegister?.salePoint?.salePointName || '-')}
              </Grid>
            </Grid>

            <Grid item xs container>
              <Grid item xs="auto">
                發票 :&nbsp;
              </Grid>
              <Grid item xs className="bold">
                {order.invoicePage ? order.invoicePage.no : '無發票資訊'}
              </Grid>
            </Grid>

            <Grid item xs container>
              <Grid item xs="auto">
                服務員 :&nbsp;
              </Grid>
              <Grid item xs className="bold">
                {order.user && order.user.realName}
              </Grid>
            </Grid>
          </Grid>
        </Grid>

        <Grid item xs="auto">
          {order.no}
        </Grid>

      </Grid>

      <OrderItemDetail list={order.salesDetails || []} />


      <Grid container justifyContent="space-between" alignItems="center"
        sx={{
          p: '0.25rem',
          borderTop: theme => `1px solid ${theme.palette.divider}`,
        }}
      >
        <Grid item xs="auto" container flexDirection="column">

          <Grid item xs container>
            <Grid item xs="auto">
              信用卡號末四碼 :&nbsp;
            </Grid>
            <Grid item xs className="bold">
              {creditCardInfo.cardNo ? creditCardInfo.cardNo.replaceAll(' ', '').slice(-4) : '- - - -'}
            </Grid>
          </Grid>

          <Grid item xs container>
            <Grid item xs="auto">
              授權碼 :&nbsp;
            </Grid>
            <Grid item xs className="bold">
              {creditCardInfo.approvalCode || '- - - -'}
            </Grid>
          </Grid>

        </Grid>

        <Grid item xs="auto">
          <Box className="received-list" display="inline-flex" sx={{
            '& > div': {
              pr: '1rem',
            },
            '& .main-value': {
              fontWeight: 'bold',
            },
          }}>
            {renderPayment((order.salesPayments || []))}
          </Box>

          <Box className="total-sum" display="inline-flex" sx={{
            fontSize: '1.25rem',
          }}>
            <span>總金額 :&nbsp;</span>
            <span className="bold">
              {`${order ? order.actualTotal : '- - -'}元`}
            </span>
          </Box>
        </Grid>
      </Grid>

    </Paper>
  )
}

export const orderTableStyle = {
  '& .table-header': {
    display: 'flex',
    color: 'background.paper',
    backgroundColor: '#a4a4a4',
    '& > div': {
      display: 'inline-flex',
      alignItems: 'center',
      justifyContent: 'center',
      textAlign: 'center',
      padding: '0.25em 0.5em',
      wordBreak: 'break-all',
      borderRight: theme => `1px solid ${theme.palette.background.paper}`,
    },
    '& > div:last-child': {
      borderRight: 'none',
    },
    '& .name-sec': {
      justifyContent: 'flex-start',
      paddingLeft: '1.5em',
      paddingRight: '1.5em',
    },
  },
  '& .table-item': {
    display: 'flex',
    color: 'text.primary',
    backgroundColor: 'background.darkPaper',
    '&:nth-of-type(2n)': {
      backgroundColor: 'background.paper',
    },
    '& > div': {
      display: 'inline-flex',
      alignItems: 'center',
      justifyContent: 'center',
      textAlign: 'center',
      padding: '0.5em 0.5em',
      wordBreak: 'break-all',
      borderRight: theme => `1px solid ${theme.palette.background.paper}`,
    },
    '& > div:last-of-type': {
      borderRight: 'none',
    },
    '& .name-sec': {
      justifyContent: 'flex-start',
      paddingLeft: '1.5em',
      paddingRight: '1.5em',
    },
  },
  '& .serial-sec': {
    flex: '0 0 5%',
    minWidth: '2rem',
  },
  '& .name-sec': {
    flex: '1 0 auto',
  },
  '& .clerk-sec': {
    flex: '0 0 10%'
  },
  '& .price-sec': {
    flex: '0 0 10%'
  },
  '& .quantity-sec': {
    flex: '0 0 10%'
  },
  '& .discount-sec': {
    flex: '0 0 10%'
  },
  '& .total-sec': {
    flex: '0 0 10%'
  },
}


export default PrintedOrder