import { v4 as uuidv4 } from 'uuid'

export const getNewStrip = (comm, priceMapping) => {
  const price = priceMapping
    ? (priceMapping[comm.stockId] || comm.price)
    : (comm.price ? comm.price.price : 0)

  return {
    id: uuidv4(),
    stockId: comm.stockId,
    stockName: comm.stockName || comm.description,
    undiscountable: comm.undiscountable,
    quantity: 1,
    discount: '',
    price: price,
    total: price,
    salePrice: price,
    originalTotal: price,
  }

}

export const mapDetail = detail => {
  return detail.map(item => {
    return {
      item: item.stockDescription || item.stockMaster.description,
      qty: item.quantity,
      salePrice: item.salePrice,
      subTotal: item.subTotal,
    }
  })
}

export const printReceipt = salesInfo => {
  const invoiceHeads = salesInfo?.cashRegister?.salePoint || {}
  const saleDetail = salesInfo?.salesDetails || []
  const actualTotal = salesInfo?.actualTotal

  window.printerBridge.printReceipt(
    {
      headTime: invoiceHeads.headTime,
      invoiceHead1: invoiceHeads.invoiceHead1 || '',
      invoiceHead2: invoiceHeads.invoiceHead2 || '',
      invoiceHead3: invoiceHeads.invoiceHead3 || '',
      invoiceHead4: invoiceHeads.invoiceHead4 || '',
      invoiceHead5: invoiceHeads.invoiceHead5 || '',
      invoiceHead6: invoiceHeads.invoiceHead6 || '',
      salePointInvoiceName: invoiceHeads.salePointInvoiceName || '',
    },
    mapDetail(saleDetail),
    actualTotal,
  )
}