import React, { useState, useRef } from 'react'
import { Box, Container } from '@mui/material'
import InfoHeader from './InfoHeader'
import ControlBar from './ControlBar'
import CommStripList from './CommStripList'
import SearchComm from './SearchComm'
import NumberPanel from './NumberPanel'
import SettlementBar from './SettlementBar'
import CommGrid from './CommGrid'
import withAuth from '../Auth/withAuth'

import { INITIAL_KEYBOARD_MODE } from '../../publicVariable'

const PosPlatform = () => {
  const [uniformContent, setUniformContent] = useState('')
  const [activeCommId, setActiveCommId] = useState('')
  const [selectedIds, setSelectedIds] = useState([])
  const [keyboardMode, setKeyboardMode] = useState(INITIAL_KEYBOARD_MODE)
  const listRef = useRef()

  return (
    <Box id="pos-platform" sx={styles.root}>
      <Container className="pos-platform-container" maxWidth={false} sx={styles.container}>
        <Box className="left-platform" sx={styles.leftPlatform}>
          <InfoHeader />
          <div className="left-container">
            <ControlBar setSelectedIds={setSelectedIds} />
            <CommStripList
              listRef={listRef}
              activeCommId={activeCommId}
              selectedIds={selectedIds}
              setUniformContent={setUniformContent}
              setActiveCommId={setActiveCommId}
              setSelectedIds={setSelectedIds}
              setKeyboardMode={setKeyboardMode}
            />

            <Box className="panel-container" sx={styles.panelContainer}>
              <SearchComm
                listRef={listRef}
                setActiveCommId={setActiveCommId}
                setUniformContent={setUniformContent}
              />
              <NumberPanel
                uniformContent={uniformContent}
                activeCommId={activeCommId}
                selectedIds={selectedIds}
                keyboardMode={keyboardMode}
                setKeyboardMode={setKeyboardMode}
                setUniformContent={setUniformContent}
              />
            </Box>
            <SettlementBar
              setUniformContent={setUniformContent}
              setSelectedIds={setSelectedIds}
            />
          </div>
        </Box>

        <Box className="right-platform" sx={styles.rightPlatform}>
          <CommGrid
            listRef={listRef}
            setActiveCommId={setActiveCommId}
            setUniformContent={setUniformContent}
          />
        </Box>
      </Container>
    </Box>
  )
}

const styles = {
  root: {
    color: '#333333',
    backgroundColor: 'background.paper',
  },
  container: {
    '&.pos-platform-container': {
      maxWidth: 1800,
      display: 'flex',
      flexWrap: 'wrap',
      minHeight: 620,
      px: 0,
    },
  },
  leftPlatform: {
    // height: 'calc(100vh - 0.75rem)',
    height: 'calc(100vh - 2.5rem)',
    display: 'flex',
    flex: '0 0 100%',
    flexDirection: 'column',
    backgroundColor: 'background.paper',
    boxShadow: 'inset -10px 0px 15px -10px #aaaaaa',
    minWidth: 0,
    '& .left-container': {
      flex: '0 1 95%',
      display: 'flex',
      flexDirection: 'column',
      width: '90%',
      margin: '0 auto',
      minHeight: 0,
      minWidth: 0,
    },
    '@media (min-width: 960px)': {
      flex: '0 0 60%',
    },
  },
  rightPlatform: {
    height: '100vh',
    position: 'relative',
    display: 'flex',
    flex: '0 0 100%',
    flexDirection: 'column',
    bgcolor: 'secondary.main',
    '@media (min-width: 960px)': {
      flex: '0 0 40%',
      height: 'calc(100vh - 2.5rem)',
    },
  },
  panelContainer: {
    flex: '0 1 40.875%',
    display: 'flex',
    minHeight: 0,
  },
}

export default withAuth()(PosPlatform)
