import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import { replacePosStrips } from '../../../slices/posStripsSlice'
import { replaceCurrentMember } from '../../../slices/currentMemberSlice'
import { getTempOrderWithLocal, deleteTempOrderWithLocal } from '../../../slices/tempOrdersSlice'
import { replaceCurrentTempOrderId } from '../../../slices/currentIdsSlice'
import { Cancel, Restore } from '@mui/icons-material'
import { Box, Button, Dialog, DialogContent, DialogActions, IconButton, Typography } from '@mui/material'


const DialogTempOrder = props => {
  const { open, handleClose, replacePosStrips, tempOrders, getTempOrderWithLocal, deleteTempOrderWithLocal, replaceCurrentTempOrderId, replaceCurrentMember } = props
  // State currentTempOrderId 用於顯示細項列表
  // Redux currentTempOrderId 用於結帳時調整訂單狀態
  const [currentTempOrderId, setCurrentTempOrderId] = useState('')

  useEffect(() => {
    getTempOrderWithLocal()
  }, [getTempOrderWithLocal])

  useEffect(() => {
    if (tempOrders[0]) setCurrentTempOrderId(tempOrders[0].id)
  }, [tempOrders])

  const onTempOrderClick = id => {
    setCurrentTempOrderId(id)
  }

  const onRetrieveClick = () => {
    replacePosStrips(currentTempOrder.content)
    replaceCurrentMember(currentTempOrder.member || {})
    replaceCurrentTempOrderId(currentTempOrderId)
    handleClose()
  }

  const formatDateString = timestamp => {
    const inputDate = new Date(timestamp);
    const yearString = inputDate.getFullYear().toString()
    const month = inputDate.getMonth() + 1
    const monthString = month > 9 ? month.toString() : '0' + month.toString()
    const date = inputDate.getDate()
    const dateString = date > 9 ? date.toString() : '0' + date.toString()
    const dayString = dayStrings[inputDate.getDay()]
    const originTimeString = inputDate.toLocaleTimeString().replace('午', '午 0').slice(0, -3)
    const timeString = originTimeString.length > 8 ? originTimeString.replace(' 01', ' 1') : originTimeString
    return yearString + '/' + monthString + '/' + dateString + ' (' + dayString + ') ' + timeString
  }

  const currentTempOrder = tempOrders.find(tempOrder => tempOrder.id === currentTempOrderId)

  const renderTempItem = tempOrders => {
    return tempOrders.map((tempOrder, index) => {
      const active = currentTempOrderId === tempOrder.id ? ' active' : ''

      return (
        <div className={`temp-item ${active}`} key={tempOrder.id} onClick={() => onTempOrderClick(tempOrder.id)}>
          <div className="serial-sec">{index + 1}</div>
          <div className="time-sec">{formatDateString(tempOrder.timestamp)}</div>
          <div className="total-amount-sec">{tempOrder.totalAmount}</div>
          <div className="total-quantity-sec">{tempOrder.totalQuantity}</div>
          <div className="member-name-sec">{tempOrder.member.memberName}</div>
          <div className="member-phone-sec">{tempOrder.member.phone}</div>
          <div className="delete-sec" onClick={() => deleteTempOrderWithLocal(tempOrder.id)}>
            <Cancel className="delete-icon" />
          </div>
        </div>
      )
    })
  }

  const renderDetailItem = details => {
    return details.map((detail, index) => (
      <div className="detail-item" key={detail.id}>
        <div className="serial-sec">{index + 1}</div>
        <div className="comm-name-sec">{detail.stockName}</div>
        <div className="price-sec">{detail.price}</div>
        <div className="quantity-sec">{detail.quantity}</div>
        <div className="discount-sec">{detail.discount || ' - '}</div>
        <div className="total-sec">{detail.total}</div>
      </div>
    ))
  }

  return (
    <Dialog
      id="dialog-temp-order"
      open={open}
      onClose={handleClose}
      aria-labelledby="dialog-temp-order"
      fullWidth maxWidth="md"
    >

      <Box sx={{
        flex: '0 0 auto',
        margin: 0,
        padding: '0.375em 1.75em 0.25em',
        lineHeight: 1.6,
        letterSpacing: '0.0075rem',
        bgcolor: 'background.default',
        '& h6': {
          color: 'secondary.main',
          fontSize: '1.25rem',
          fontWeight: 'bold',
          letterSpacing: '0.05em',
        },
      }}>
        <Typography variant="h6">訂單暫存</Typography>
        {handleClose ? (
          <IconButton aria-label="close" onClick={handleClose} sx={{
            position: 'absolute',
            right: '0.5em',
            top: '0.25em',
            color: '#cb5c5c',
            padding: 0,
          }}>
            <Cancel sx={{ width: '1.25em', height: '1.25em' }} />
          </IconButton>
        ) : null}
      </Box>

      <DialogContent sx={{ padding: '0.5em 1.75em 0', bgcolor: 'background.paper', }}>
        <Typography variant="h6" sx={{
          color: (theme) => theme.palette.primary.main,
          fontSize: '1.25rem',
          fontWeight: 'bold',
          letterSpacing: '0.05em',
        }}>暫存清單</Typography>
        <Box sx={tempHeaderStyle}>
          <div className="serial-sec">項次</div>
          <div className="time-sec">暫存時間</div>
          <div className="total-amount-sec">總金額</div>
          <div className="total-quantity-sec">總數量</div>
          <div className="member-name-sec">會員姓名</div>
          <div className="member-phone-sec">會員電話</div>
          <div className="delete-sec"></div>
        </Box>
        <Box sx={tempListStyle}>
          {renderTempItem(tempOrders)}
        </Box>

        <hr />
        <Typography variant="h6" sx={{
          color: (theme) => theme.palette.primary.main,
          fontSize: '1.25rem',
          fontWeight: 'bold',
          letterSpacing: '0.05em',
        }}>清單明細</Typography>

        <Box sx={detailHeaderStyle}>
          <div className="serial-sec">項次</div>
          <div className="comm-name-sec">商品名稱</div>
          <div className="price-sec">定價</div>
          <div className="quantity-sec">數量</div>
          <div className="discount-sec">折數</div>
          <div className="total-sec">合計</div>
        </Box>

        <Box sx={detailListStyle}>
          {currentTempOrder ? renderDetailItem(currentTempOrder.content) : '無資料'}
        </Box>
      </DialogContent>

      <DialogActions sx={{
        padding: '1em 1.75em',
        bgcolor: 'background.paper'
      }}>
        <Button
          onClick={onRetrieveClick}
          startIcon={<Restore sx={{ '&:first-of-type': { fontSize: '1.25em' } }} />}
          sx={{
            display: 'inline-flex',
            justifyContent: 'center',
            fontSize: '1.375rem',
            fontWeight: 'bold',
            borderRadius: '0.5em',
            border: 'none',
            color: 'background.paper',
            bgcolor: (theme) => theme.palette.primary.main,
            minWidth: 105,
            '&:hover': {
              bgcolor: '#fabd5e',
            },
          }}
        >取回</Button>
      </DialogActions>

    </Dialog>
  )
}

const dayStrings = ['日', '一', '二', '三', '四', '五', '六']

const tempHeaderStyle = {
  display: 'flex',
  color: 'background.darkPaper',
  bgcolor: 'secondary.main',
  '& > div': {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    fontSize: '1rem',
    borderRight: theme => `1px solid ${theme.palette.background.darkPaper}`,
    paddingTop: '0.125em',
    paddingBottom: '0.125em',
  },
  '& .serial-sec': {
    flex: '0 0 5%',
    borderLeft: theme => `1px solid ${theme.palette.background.darkPaper}`,
  },
  '& .time-sec': {
    flex: '0 0 30%',
  },
  '& .total-amount-sec': {
    flex: '0 0 12.5%',
  },
  '& .total-quantity-sec': {
    flex: '0 0 12.5%',
  },
  '& .member-name-sec': {
    flex: '0 0 17.5%',
  },
  '& .member-phone-sec': {
    flex: '0 0 17.5%',
  },
  '& .delete-sec': {
    flex: '0 0 5%',
    borderRight: 'none',
  }
}

